import { MetricsReporter, UpCounter, ValueRecorder } from '../interfaces';

import { BucketHistogramImpl } from './BucketHistogramImpl';
import { UpCounterImpl } from './UpCounterImpl';
import { ValueRecorderImpl } from './ValueRecorderImpl';
import { SizeLimitedMap } from './SizeLimitedMap';
import { MetricsTags } from 'o11y/shared';

const maxCounters = 1000;
const maxValueRecorders = 500;
const maxBucketHistograms = 500;

enum MetricType {
    Counter,
    Percentile,
    BucketHistogram
}

export class MetricsImpl implements MetricsReporter {
    private readonly _upCounters = new SizeLimitedMap<string, UpCounterImpl>(maxCounters);
    private readonly _valueRecorders = new SizeLimitedMap<string, ValueRecorderImpl>(
        maxValueRecorders
    );
    private readonly _bucketHistograms = new SizeLimitedMap<string, BucketHistogramImpl>(
        maxBucketHistograms
    );

    constructor(
        private readonly _ownerName: string,
        private readonly _getOwnerAppName: () => string
    ) {}

    incrementCounter(
        operation: string,
        increment: number,
        hasError = false,
        tags: MetricsTags = {}
    ): void {
        this._tagError(tags, hasError);
        this._upCounter(operation, tags).increment(increment);
    }

    trackValue(operation: string, value: number, hasError = false, tags: MetricsTags = {}): void {
        this._tagError(tags, hasError);
        this._valueRecorder(operation, tags).record(value);
    }

    bucketValue(
        operation: string,
        value: number,
        buckets: number[],
        hasError = false,
        tags: MetricsTags = {}
    ): void {
        this._tagError(tags, hasError);
        this._bucketHistogram(operation, tags, buckets).record(value);
    }

    private _bucketHistogram(name: string, tags: MetricsTags, buckets: number[]): ValueRecorder {
        const { key, sortedTags } = this._getKeyAndSortedTags(
            name,
            tags,
            MetricType.BucketHistogram
        );
        let instrument = this._bucketHistograms.get(key);
        if (!instrument) {
            instrument = new BucketHistogramImpl(
                name,
                this._ownerName,
                this._getOwnerAppName(),
                sortedTags,
                buckets
            );
            if (!this._bucketHistograms.set(key, instrument)) {
                throw new Error(`Max size of ${maxBucketHistograms} exceeded for BucketHistograms`);
            }
        }
        return instrument;
    }

    private _upCounter(name: string, tags: MetricsTags): UpCounter {
        const { key, sortedTags } = this._getKeyAndSortedTags(name, tags, MetricType.Counter);
        let instrument = this._upCounters.get(key);
        if (!instrument) {
            instrument = new UpCounterImpl(
                name,
                this._ownerName,
                this._getOwnerAppName(),
                sortedTags
            );
            if (!this._upCounters.set(key, instrument)) {
                throw new Error(`Max size of ${maxCounters} exceeded for UpCounters`);
            }
        }
        return instrument;
    }

    private _valueRecorder(name: string, tags: MetricsTags): ValueRecorder {
        const { key, sortedTags } = this._getKeyAndSortedTags(name, tags, MetricType.Percentile);
        let instrument = this._valueRecorders.get(key);
        if (!instrument) {
            instrument = new ValueRecorderImpl(
                name,
                this._ownerName,
                this._getOwnerAppName(),
                sortedTags
            );
            if (!this._valueRecorders.set(key, instrument)) {
                throw new Error(`Max size of ${maxValueRecorders} exceeded for ValueRecorders`);
            }
        }
        return instrument;
    }

    getUpCounters(): UpCounterImpl[] {
        return this._upCounters.getElements();
    }

    getValueRecorders(): ValueRecorderImpl[] {
        return this._valueRecorders.getElements();
    }

    getBucketHistograms(): BucketHistogramImpl[] {
        return this._bucketHistograms.getElements();
    }

    private _tagError(tags: MetricsTags, hasError: boolean): void {
        tags.status = hasError === true ? 'error' : 'success';
    }

    private _getKeyAndSortedTags(
        operation: string,
        tags: MetricsTags,
        metricType: MetricType
    ): { key: string; sortedTags: MetricsTags } {
        // This method should be called after any manipulation of tags
        const sortedTags = Object.keys(tags)
            .sort()
            .reduce((prev: MetricsTags, curr: string) => {
                prev[curr] = tags[curr];
                return prev;
            }, {});
        const key = `${MetricType[metricType]}:${operation}${JSON.stringify(sortedTags)}`;
        return {
            key,
            sortedTags
        };
    }
}
