// This is the file that exports all `o11y` module level exports.
import { idleDetectorReportSchema } from 'o11y_schema/sf_instrumentation';
import { IdleDetector, IdleDetectorReport } from './interfaces';
import { IdleDetectorImpl } from './library/IdleDetectorImpl';
import { time, Index } from './index';

export const idleDetector: IdleDetector = new IdleDetectorImpl({
    logThreshold: 300,
    reportListener: (report: IdleDetectorReport) => {
        idleDetectorInstr?.log(idleDetectorReportSchema, report);
    },
    errorListener: (e: unknown, text?: string) => {
        idleDetectorInstr?.error(e, text);
    }
});
const index = new Index(idleDetector); // singleton
const registerInstrumentedApp = index.registerInstrumentedApp.bind(index);
const getInstrumentation = index.getInstrumentation.bind(index);

const idleDetectorInstr = getInstrumentation('IdleDetector');

export { registerInstrumentedApp, getInstrumentation, time };
export { ConsoleCollector } from './console-collector/ConsoleCollector';
export { version as _version } from './version';
