import { PublicSafety, SchematizedData, TraceHeaders, utility } from 'o11y/shared';

import {
    Activity,
    ActivityDetail,
    ActivityStopOptions,
    Schema,
    TracingHeadersOptions
} from '../interfaces';
import { Tracing } from './Tracing';
import { TimerOverride } from './TimerOverride';
export const timedout = 'timedout';
export const terminated = 'terminated';
export const discarded = 'discarded';

const stopReason = {
    timedout,
    terminated,
    discarded
};

export type onActivityStop = (detail: ActivityDetail) => void;
export type onActivityError = (error: unknown, detail: ActivityDetail) => void;

export class ActivityImpl implements Activity {
    protected readonly _id: string;
    private readonly _safety: PublicSafety;
    private readonly _usePerf: boolean;
    protected static _count = 0;
    private readonly _perfId: string;
    private readonly _perfName: string;
    private _startTimestamp: number;
    private _startPerfTime: number;
    private _stopPerfTime: number;
    private _stopReason: string;
    private _timer: NodeJS.Timer;
    private _errorCount = 0;
    private _timerOverridden: TimerOverride = TimerOverride.none;

    constructor(
        protected readonly _name: string,
        protected readonly _rootId: string,
        private readonly _onStopped: onActivityStop,
        private readonly _onError: onActivityError,
        timeout?: number,
        id?: string,
        protected readonly _isSampled?: boolean
    ) {
        const { tsNow, perfNow } = utility.time();

        ActivityImpl._count += 1;
        this._id = id || utility.generateUniqueId();

        this._usePerf =
            typeof performance !== 'undefined' &&
            typeof performance.mark === 'function' &&
            typeof performance.measure === 'function';
        if (this._usePerf) {
            this._perfName = `${this._name}__${ActivityImpl._count}`;
            this._perfId = `${this._name}__${this.id}`;
            try {
                performance.mark(this._perfId);
            } catch {
                this._usePerf = false;
            }
        }

        this._startTimestamp = tsNow;
        this._startPerfTime = perfNow;

        if (timeout > 0) {
            this._timer = setTimeout(() => {
                this._stopReason = this._stopReason || stopReason.timedout;
                this.stop();
            }, timeout);
        }
        this._safety = new PublicSafety();
    }

    get id(): string {
        return this._id;
    }

    getId(): string {
        return this.id;
    }

    getRootId(): string {
        return this._rootId;
    }

    get stopReason(): string {
        return this._stopReason;
    }

    error(error: unknown, userSchemaOrText?: Schema | string, userData?: SchematizedData): void {
        this._safety.tryCatch(() => {
            this._errorCount += 1;
            if (typeof error === 'string') {
                error = new Error(error);
            }
            this._onError(error, this._getDetail(userSchemaOrText, userData));
        });
    }

    stop(
        userSchemaOrText?: Schema | string,
        userData?: SchematizedData,
        options?: ActivityStopOptions
    ): void {
        this._safety.tryCatch(() => {
            utility.requireArgumentIfDefined(userSchemaOrText, 'userSchemaOrText', [
                'object',
                'string'
            ]);
            utility.requireArgumentIfDefined(userData, 'userData', 'object');

            if (options) {
                const perfStart: number = options.perfStartOverride;
                const isStartTimeDefined = utility.requireArgumentIfDefined(
                    perfStart,
                    'options.perfStartOverride',
                    'number'
                );

                const perfStop: number = options.perfStopOverride;
                const isStopTimeDefined = utility.requireArgumentIfDefined(
                    perfStop,
                    'options.perfStopOverride',
                    'number'
                );

                if (isStartTimeDefined && perfStart < 0) {
                    throw new Error('perfStartOverride, if defined, must be >= 0');
                }

                if (
                    isStopTimeDefined &&
                    (perfStop < 0 ||
                        perfStop < (isStartTimeDefined ? perfStart : this._startPerfTime))
                ) {
                    throw new Error('perfStopOverride, if defined, must be >= 0 and >= startTime');
                }
            }

            this._stopInternal(userSchemaOrText, userData, options);
        });
    }

    private _overrideTimer(perfStartTime?: number, perfStopTime?: number): void {
        if (perfStartTime !== undefined) {
            this._startTimestamp = Math.round(
                this._startTimestamp - (this._startPerfTime - perfStartTime)
            );
            this._startPerfTime = perfStartTime;
            this._setTimerOverride(TimerOverride.start);
        }

        if (perfStopTime !== undefined) {
            this._stopPerfTime = perfStopTime;
            this._setTimerOverride(TimerOverride.stop);
        }
    }

    private _setTimerOverride(value: TimerOverride): void {
        if (
            this._timerOverridden === TimerOverride.both ||
            this._timerOverridden === value ||
            value === TimerOverride.none
        ) {
            return;
        }
        this._timerOverridden =
            this._timerOverridden === TimerOverride.none ? value : TimerOverride.both;
    }

    private _stopInternal(
        userSchemaOrText: Schema | string,
        userData: SchematizedData,
        options?: ActivityStopOptions
    ): void {
        // First things first.
        const stopPerfTime = utility.perfNow();
        if (this._usePerf) {
            try {
                // Use a more user-friendly name for measure.
                if (this._stopReason !== stopReason.discarded) {
                    performance.measure(this._perfName, this._perfId);
                }
                performance.clearMarks(this._perfId);
                performance.clearMeasures(this._perfName);
            } catch {}
        }

        if (this._timer) {
            clearTimeout(this._timer);
            this._timer = undefined;
        }
        if (this.isStopped) {
            // Already stopped
            return;
        }

        if (options) {
            this._overrideTimer(options.perfStartOverride, options.perfStopOverride);
        }

        // We may set this already in override so check that here
        this._stopPerfTime =
            this._timerOverridden === TimerOverride.none ||
            this._timerOverridden === TimerOverride.start
                ? stopPerfTime
                : this._stopPerfTime;

        const activityDetail: ActivityDetail = this._getDetail(userSchemaOrText, userData);
        this._onStopped(activityDetail);
    }

    get isStopped(): boolean {
        return this._stopPerfTime !== undefined;
    }

    discard(): void {
        this._safety.tryCatch(() => {
            this._stopReason = this._stopReason || stopReason.discarded;
            this.stop();
        });
    }

    terminate(
        userSchemaOrText?: Schema | string,
        userData?: SchematizedData,
        options?: ActivityStopOptions
    ): void {
        this._safety.tryCatch(() => {
            this._stopReason = this._stopReason || stopReason.terminated;
            this.stop(userSchemaOrText, userData, options);
        });
    }

    getIsSampled(): boolean {
        return this._isSampled || false;
    }

    protected _getDetail(
        userSchemaOrText: Schema | string,
        userData: SchematizedData
    ): ActivityDetail {
        return {
            id: this._id,
            rootId: this._rootId,
            name: this._name,
            userSchemaOrText: userSchemaOrText,
            userData,
            stopReason: this._stopReason,
            startTimestamp: this._startTimestamp,
            startPerfTime: this._startPerfTime,
            stopPerfTime: this._stopPerfTime,
            errorCount: this._errorCount,
            isSampled: this.getIsSampled(),
            timerOverridden: this._timerOverridden
        };
    }

    public getTraceHeaders(options: TracingHeadersOptions): TraceHeaders {
        // Normally, the root activity ID is the traceId.
        // If the activity is root, it's ID is (also) the traceId.
        // If the activity has no root, it's ID is (also) the traceId. i.e. It creates its own trace.
        const spanId = this.id;
        const traceId = this._rootId || spanId;

        return Tracing.getHeaders(traceId, spanId, this.getIsSampled(), options);
    }

    getStartTimestamp(): number {
        return this._startTimestamp;
    }

    getStartPerfTime(): number {
        return this._startPerfTime;
    }

    getStopPerfTime(): number {
        return this._stopPerfTime;
    }
}
