import { SchematizedPayload } from '../interfaces';
import { InstrumentedEventData, MouseEventData } from '../interfaces-private';
import { DomEventData } from '../types/TypeDefinitions-Private';
import { schemaUtil, utility } from 'o11y/shared';

export class DomEventHelpers {
    getInstrumentedEventData(
        eventData: DomEventData,
        handledBy: HTMLElement,
        userPayload: SchematizedPayload
    ): InstrumentedEventData {
        const instrumentedEvent: InstrumentedEventData = {
            ownerComponent: handledBy.tagName,
            parentComponent: handledBy.parentElement?.tagName || undefined,
            event: eventData,
            simplePath: utility.getXpath(handledBy)
        };

        if (userPayload) {
            instrumentedEvent.userPayload = schemaUtil.makePayload(
                userPayload.schema,
                userPayload.payload,
                true
            );
        }

        return instrumentedEvent;
    }

    getMouseEventData(e: MouseEvent): MouseEventData {
        return {
            altKey: e.altKey,
            bubbles: e.bubbles,
            button: e.button,
            buttons: e.buttons,
            cancelable: e.cancelable,
            clientX: e.clientX,
            clientY: e.clientY,
            composed: e.composed,
            defaultPrevented: e.defaultPrevented,
            detail: e.detail,
            eventPhase: e.eventPhase,
            isTrusted: e.isTrusted,
            timeStamp: e.timeStamp,
            type: e.type,
            ctrlKey: e.ctrlKey,
            metaKey: e.metaKey,
            movementX: e.movementX,
            movementY: e.movementY,
            offsetX: e.offsetX,
            offsetY: e.offsetY,
            pageX: e.pageX,
            pageY: e.pageY,
            screenX: e.screenX,
            screenY: e.screenY,
            shiftKey: e.shiftKey
        };
    }
}

const singleton = new DomEventHelpers();
export const domEventHelpers = singleton;
