import { RootActivity, TracingHeadersOptions } from '../interfaces';

const rootId: string = new Array(32).fill(0).join('');
class RootActivityNoOp implements RootActivity {
    getId(): string {
        return rootId;
    }
    getRootId(): string {
        return undefined;
    }
    /* eslint-disable-next-line @typescript-eslint/no-empty-function */
    error(): void {}

    /* eslint-disable-next-line @typescript-eslint/no-empty-function */
    stop(): void {}

    /* eslint-disable-next-line @typescript-eslint/no-empty-function */
    discard(): void {}

    /* eslint-disable-next-line @typescript-eslint/no-empty-function */
    terminate(): void {}

    getIsSampled(): boolean {
        return false;
    }
    getTraceHeaders(__options: TracingHeadersOptions): Record<string, string> {
        return {};
    }
    getStartTimestamp(): number {
        return undefined;
    }
    getStartPerfTime(): number {
        return undefined;
    }
    getStopPerfTime(): number {
        return undefined;
    }
}

export const rootActivityNoOp = Object.freeze(new RootActivityNoOp());
