import { TraceHeaders, utility } from 'o11y/shared';
import {
    Instrumentation,
    TracingHeadersOptions,
    NetworkInstrumentationOptions,
    Activity
} from '../interfaces';
import { IdleDetector, TaskerMulti } from '../interfaces/IdleDetector';

type FetchType = (resource: RequestInfo | URL, options?: RequestInit) => Promise<Response>;

type O11yFetchOptions = {
    skipInstr?: boolean;
};

type O11yFetchType = (
    resource: RequestInfo | URL,
    options?: RequestInit & { o11y?: O11yFetchOptions }
) => Promise<Response>;

type XhrType = typeof XMLHttpRequest;

type XhrOpenType = (
    method: string,
    url: string | URL,
    async?: boolean,
    username?: string | null,
    password?: string | null
) => void;

type XhrSendType = (body?: Document | XMLHttpRequestBodyInit | null) => void;

type O11yInfo = {
    activity?: Activity;
    method?: string;
    url?: string;
};
interface O11yXhr extends XMLHttpRequest {
    _o11y: O11yInfo;
}

const defaultTraceIdLengthW3 = 32; // 32 Characters = 128 bits
const defaultTraceIdLengthB3 = 16; // 16 Characters = 64 bits
const defaultSpanIdLength = 16; // 16 Characters = 64 bits

// Headers for W3 tracing (https://www.w3.org/TR/trace-context/#traceparent-header)
const headerTraceParent = 'traceparent';

// Headers for B3 tracing (https://github.com/openzipkin/b3-propagation)
const headerB3 = 'b3';
const headerTraceId = 'X-B3-TraceId';
const headerSpanId = 'X-B3-SpanId';
const headerSampled = 'X-B3-Sampled';
const headerParentSpanId = 'X-B3-ParentSpanId';

const defaultActivityNameForFetch = 'fetch';
const defaultActivityNameForXhrSend = 'xhr_send';

export class Tracing {
    private static _global: typeof globalThis = utility.getGlobal();
    private static _originalFetch: FetchType;
    private static _originalXhr: XhrType;
    private static _originalXhrOpen: XhrOpenType;
    private static _originalXhrSend: XhrSendType;
    private static _isNetworkInstrumentationEnabled = false;
    private static _reqIndex = 0;

    constructor(
        private readonly _instr: Instrumentation,
        private readonly _idleDetector: IdleDetector
    ) {
        utility.requireArgument(_instr, '_instr', 'object');
        utility.requireArgument(_idleDetector, '_idleDetector', 'object');
    }

    private static _getB3CompactFormat(
        traceId: string,
        spanId: string,
        isSampled?: boolean,
        parentSpanId?: string
    ): string {
        // b3={TraceId}-{SpanId}-{SamplingState}-{ParentSpanId}
        // Note: last two fields are optional

        let b3 = `${traceId}-${spanId}`;

        if (isSampled !== undefined || parentSpanId !== undefined) {
            b3 += isSampled ? '-1' : '-0';
            if (parentSpanId !== undefined) {
                b3 += `-${parentSpanId}`;
            }
        }
        return b3;
    }

    private static _getW3CompactFormat(
        traceId: string,
        spanId: string,
        isSampled: boolean
    ): string {
        // traceparent={version}-{trace-id}-{parent-id}-{trace-flags}
        // "in some tracing systems, parent-id is known as the span-id, where a span is the execution of a client request"
        return `00-${traceId}-${spanId}-${isSampled ? '01' : '00'}`;
    }

    private static _conformTextAsId(text: string, numChars: number): string {
        // First validate
        text = text.toLowerCase();
        const len: number = text.length;
        for (let i = 0; i < len; i += 1) {
            const c = text[i];
            if (!(c >= 'a' && c <= 'f') && !(c >= '0' && c <= '9')) {
                throw new Error(`The text '${text}' has an invalid character at index #${i}`);
            }
        }

        if (len > numChars) {
            // Use the right-most characters per spec
            return text.substr(len - numChars);
        }
        if (len < numChars) {
            // Pad left with 0s per spec
            return text.padStart(numChars, '0');
        }
        return text;
    }

    public static getHeaders(
        traceId: string,
        spanId: string,
        isSampled?: boolean,
        options?: TracingHeadersOptions
    ): TraceHeaders {
        utility.requireArgument(traceId, 'traceId', 'string');
        utility.requireArgument(spanId, 'spanId', 'string');

        const useB3Headers: boolean = options?.useB3Headers;

        traceId = this._conformTextAsId(
            traceId,
            options?.traceIdEffectiveLength !== undefined
                ? options.traceIdEffectiveLength
                : useB3Headers
                ? defaultTraceIdLengthB3
                : defaultTraceIdLengthW3
        );
        spanId = this._conformTextAsId(spanId, defaultSpanIdLength);

        const parentSpanId =
            (options &&
                options.parentSpanId !== undefined &&
                this._conformTextAsId(options.parentSpanId, defaultSpanIdLength)) ||
            undefined;

        const headers: TraceHeaders = {};

        if (useB3Headers) {
            // By default, use non-compact
            const isCompact: boolean = options?.useCompactHeader;
            if (isCompact) {
                headers[headerB3] = this._getB3CompactFormat(
                    traceId,
                    spanId,
                    isSampled,
                    parentSpanId
                );
            } else {
                headers[headerTraceId] = traceId;
                headers[headerSpanId] = spanId;
                if (parentSpanId !== undefined) {
                    headers[headerParentSpanId] = parentSpanId;
                }
                if (isSampled !== undefined) {
                    headers[headerSampled] = isSampled ? '1' : '0';
                }
            }
        } else {
            headers[headerTraceParent] = this._getW3CompactFormat(traceId, spanId, isSampled);
        }

        return headers;
    }

    private _disableNetworkInstrumentation(): void {
        if (Tracing._isNetworkInstrumentationEnabled) {
            if (Tracing._originalFetch) {
                Tracing._global.fetch = Tracing._originalFetch;
                Tracing._originalFetch = undefined;
            }
            if (Tracing._originalXhr) {
                Tracing._global.XMLHttpRequest = Tracing._originalXhr;
                Tracing._originalXhr = undefined;
            }
            if (Tracing._originalXhrOpen) {
                Tracing._global.XMLHttpRequest.prototype.open = Tracing._originalXhrOpen;
                Tracing._originalXhrOpen = undefined;
            }
            if (Tracing._originalXhrSend) {
                Tracing._global.XMLHttpRequest.prototype.send = Tracing._originalXhrSend;
                Tracing._originalXhrSend = undefined;
            }
            Tracing._isNetworkInstrumentationEnabled = false;
        }
    }

    private _enableNetworkInstrumentation(
        instr: Instrumentation,
        logErrors: boolean,
        activityName: string,
        useTracing: boolean,
        options: TracingHeadersOptions,
        tasker: TaskerMulti
    ): void {
        if (typeof Tracing._global.fetch === 'function') {
            this._overrideFetch(instr, logErrors, activityName, useTracing, options, tasker);
            Tracing._isNetworkInstrumentationEnabled = true;
        }

        if (typeof Tracing._global.XMLHttpRequest === 'function') {
            this._overrideXhr(instr, logErrors, activityName, useTracing, options, tasker);
            Tracing._isNetworkInstrumentationEnabled = true;
        }
    }

    private static _getTraceHeaders(
        activity: Activity | undefined,
        options: TracingHeadersOptions | undefined,
        method: string,
        url: string
    ): TraceHeaders {
        let traceHeaders: TraceHeaders = activity?.getTraceHeaders(options) || {};
        if (typeof options?.headerProcessor === 'function') {
            traceHeaders = options.headerProcessor({
                method,
                url,
                defaultHeaders: traceHeaders
            });
        } else if (typeof window === 'object') {
            // By default, only send to the original domain on browsers
            let newUrl;
            try {
                newUrl = new URL(url); // no base argument - will throw on non-absolute URLs
            } catch {
                // Relative URL.
            }
            // Check host (not hostname) to ensure port numbers match too.
            if (newUrl && newUrl.host !== window.location.host) {
                traceHeaders = undefined;
            }
        }
        return traceHeaders || {};
    }

    private _overrideFetch(
        instr: Instrumentation,
        logErrors: boolean,
        activityName: string,
        useTracing: boolean,
        tracingHeadersOptions: TracingHeadersOptions,
        tasker: TaskerMulti
    ): void {
        Tracing._originalFetch = Tracing._global.fetch;
        const o11yFetch: O11yFetchType = async (resource, options, ...restArgs) => {
            // Note: We can't make any assumptions about the arguments before we pass them to the
            // original fetch. Invalid arguments should not cause us to throw, and be
            // passed to the original fetch as-is.
            let activity: Activity;
            try {
                tasker?.add();
                const ofo: O11yFetchOptions = options?.o11y;

                if (!ofo?.skipInstr) {
                    activity = instr.startActivity(activityName || defaultActivityNameForFetch);
                } else {
                    Tracing._reqIndex += 1;
                    try {
                        // Best effort -- we can't modify other types of resources
                        if (resource instanceof URL) {
                            resource.searchParams.append('o11y', Tracing._reqIndex.toString());
                        } else if (typeof resource === 'string') {
                            const urlObj = new URL(resource);
                            const kv = `o11y=${Tracing._reqIndex}`;
                            urlObj.search += urlObj.search.startsWith('?') ? `&${kv}` : kv;
                            resource = urlObj.toString();
                        }
                    } catch {}
                }
                delete options?.o11y;

                if (useTracing) {
                    let method: string = options?.method;
                    let url: string = resource?.toString();
                    if (resource instanceof Request) {
                        if (!method) {
                            // the method specifed in options overrides the method in the resource
                            method = resource.method;
                        }
                        url = resource.url;
                    }
                    const traceHeaders = Tracing._getTraceHeaders(
                        activity,
                        tracingHeadersOptions,
                        method,
                        url
                    );

                    if (
                        !this._applyTraceHeaders(resource, options, traceHeaders) &&
                        options === undefined
                    ) {
                        options = {
                            headers: traceHeaders
                        };
                    }
                }

                return await Tracing._originalFetch.call(
                    Tracing._global,
                    resource,
                    options,
                    ...restArgs
                );
            } catch (ex) {
                if (logErrors) {
                    if (activity) {
                        activity.error(ex);
                    } else {
                        instr.error(ex);
                    }
                }
                throw ex;
            } finally {
                activity?.stop();
                tasker?.done();
            }
        };
        Tracing._global.fetch = o11yFetch;
    }

    private _overrideXhr(
        instr: Instrumentation,
        logErrors: boolean,
        activityName: string,
        useTracing: boolean,
        options: TracingHeadersOptions,
        tasker: TaskerMulti
    ): void {
        Tracing._originalXhr = Tracing._global.XMLHttpRequest;
        Tracing._originalXhrOpen = Tracing._originalXhr.prototype.open;
        Tracing._originalXhrSend = Tracing._originalXhr.prototype.send;

        Tracing._originalXhr.prototype.open = function (
            this: O11yXhr,
            method: string,
            url: string | URL,
            async?: boolean,
            username?: string | null,
            password?: string | null
        ) {
            this._o11y = {
                method,
                url: url?.toString()
            };

            Tracing._originalXhrOpen.call(this, method, url, async, username, password);
        };

        Tracing._originalXhr.prototype.send = function (this: O11yXhr, ...args) {
            let activity: Activity;
            try {
                tasker?.add();
                // eslint-disable-next-line @typescript-eslint/no-this-alias
                const xhr: O11yXhr = this;
                xhr._o11y = xhr._o11y || {};
                xhr._o11y.activity = activity = instr.startActivity(
                    activityName || defaultActivityNameForXhrSend
                );
                if (useTracing) {
                    const traceHeaders = Tracing._getTraceHeaders(
                        activity,
                        options,
                        xhr._o11y.method,
                        xhr._o11y.url
                    );

                    Object.entries(traceHeaders).forEach((entry) => {
                        xhr.setRequestHeader(entry[0], entry[1]);
                    });
                }
                Tracing._originalXhrSend.call(this, ...args);
            } catch (ex) {
                if (logErrors) {
                    if (activity) {
                        activity.error(ex);
                    } else {
                        instr.error(ex);
                    }
                }
                activity?.stop();
                tasker?.done();
                throw ex;
            }
        };

        const xhrProxy = new Proxy<XhrType>(Tracing._originalXhr, {
            construct(target: XhrType) {
                const newXhr = new target() as O11yXhr;

                // Be the first in the event chain
                newXhr.addEventListener('load', () => {
                    newXhr._o11y?.activity?.stop();
                    tasker?.done();
                });
                newXhr.addEventListener(
                    'error',
                    (__err: ProgressEvent<XMLHttpRequestEventTarget>) => {
                        const error = new Error('XHR Network-Level Error');
                        const activity = newXhr._o11y?.activity;
                        if (activity) {
                            if (logErrors) {
                                activity.error(error);
                            }
                            activity.stop();
                        } else if (logErrors) {
                            instr.error(error);
                        }
                        tasker?.done();
                    }
                );
                newXhr.addEventListener('abort', () => {
                    newXhr._o11y?.activity?.discard();
                    tasker?.done();
                });
                return newXhr;
            }
        });

        Tracing._global.XMLHttpRequest = xhrProxy;
    }

    networkInstrumentation(options: boolean | NetworkInstrumentationOptions): void {
        utility.requireArgument(options, 'options', ['boolean', 'object']);

        // First disable it if necessary, so we can use the new options if it was already enabled
        this._disableNetworkInstrumentation();
        if (options) {
            const tracingOptions: NetworkInstrumentationOptions =
                typeof options === 'object' ? options : {};

            let tasker: TaskerMulti = undefined;
            if (utility.definedValueOrDefault(tracingOptions.useTasker, true)) {
                tasker = this._idleDetector?.declareNotifierTaskMulti('o11y network');
            }

            this._enableNetworkInstrumentation(
                utility.definedValueOrDefault(tracingOptions.instrumentation, this._instr),
                utility.definedValueOrDefault(tracingOptions.logErrors, true),
                tracingOptions.activityName,
                utility.definedValueOrDefault(tracingOptions.useTracing, true),
                tracingOptions.tracingHeadersOptions,
                tasker
            );
        }
    }

    resetRequestCounter(): void {
        Tracing._reqIndex = 0;
    }

    // returns true if the headers were applied to either the options or the resource.
    private _applyTraceHeaders(
        resource: RequestInfo | URL,
        options: RequestInit,
        traceHeaders: TraceHeaders
    ): boolean {
        if (options?.headers !== undefined) {
            // if headers are defined in the options,
            // they trump any headers defined in the resource

            if (options.headers instanceof Headers && typeof options.headers.set === 'function') {
                Object.entries(traceHeaders).forEach(([key, value]) => {
                    // This headers resolves to the interface defined in
                    // lib.dom.iterable.d.ts, which doesn't include the set method.
                    (options.headers as Headers).set(key, value);
                });
                return true;
            }
            if (Array.isArray(options.headers)) {
                Object.entries(traceHeaders).forEach((entry) => {
                    (options.headers as [string, string][]).push(entry);
                });
                return true;
            }
            if (typeof options.headers === 'object') {
                options.headers = Object.assign(options.headers, traceHeaders);
                return true;
            }
        } else if (resource instanceof Request && resource.headers instanceof Headers) {
            Object.entries(traceHeaders).forEach(([key, value]) => {
                // This headers resolves to the interface defined in lib.dom.d.ts
                resource.headers.set(key, value);
            });
            return true;
        } else if (options) {
            options.headers = Object.assign({}, traceHeaders);
            return true;
        }
        return false;
    }
}
