import {
    IdleDetector,
    Instrumentation,
    InstrumentedAppMethods,
    InstrumentedAppOptions
} from './interfaces';
import { NextgenImpl } from './library/NextgenImpl';
import { utility } from 'o11y/shared';

export class Index {
    private _nextgen: NextgenImpl;
    // We are lazily initializing the singleton to allow any options to be processed
    // before the instantiation.
    private get _lazyNextGen(): NextgenImpl {
        if (!this._nextgen) {
            this._nextgen = new NextgenImpl(this._idleDetector);
        }
        return this._nextgen;
    }

    constructor(private readonly _idleDetector: IdleDetector) {
        utility.requireArgument(_idleDetector, '_idleDetector');
    }

    public registerInstrumentedApp(
        name: string,
        options?: InstrumentedAppOptions
    ): InstrumentedAppMethods {
        if (!options || options.isProduction !== false) {
            // Do this before construction
            // Production by default
            utility.markProduction();
        }
        const retVal: InstrumentedAppMethods = this._lazyNextGen.registerApp(name, options);
        if (options) {
            this._nextgen.appPayloadProvider = options.appPayloadProvider;
            this._nextgen.pagePayloadProvider = options.pagePayloadProvider;
        }
        return retVal;
    }

    public getInstrumentation(name?: string): Instrumentation {
        return this._lazyNextGen.getInstrumentation(name);
    }
}

export const time = utility.time.bind(utility);
