import { IdleDetector, IdleDetectorOptions, IdleDetectorReport } from '../interfaces';
import {
    ErrorListener,
    IdleDetectedListener,
    IsBusyChecker,
    ReportListener,
    TaskerMulti,
    TaskerSingle
} from '../interfaces/IdleDetector';
import { TaskerImpl } from './TaskerImpl';
import { utility } from 'o11y/shared';

const defaultLogThreshold = 300;

type TaskReportEntry = {
    name: string;
    start: number;
    end: number;
};

type PollableReportEntry = {
    name: string;
    count: number;
};

export class IdleDetectorImpl implements IdleDetector {
    private readonly _taskers = new Map<TaskerImpl, TaskReportEntry>();
    private readonly _listeners = new Set<IdleDetectedListener>();
    private readonly _busyCheckers = new Map<IsBusyChecker, string>();
    private readonly _logThreshold: number = defaultLogThreshold;
    private readonly _endedTasks = new Array<TaskReportEntry>();
    private readonly _pollableReport = new Array<PollableReportEntry>();
    private _firstCallbackTime: number;
    private _timerCheckQueued: NodeJS.Timeout;
    private readonly _reportListener: ReportListener;
    private readonly _errorListener: ErrorListener;

    constructor(options?: IdleDetectorOptions) {
        if (
            utility.requireArgumentIfDefined(
                options?.logThreshold,
                'options.logThreshold',
                'number'
            )
        ) {
            this._logThreshold = options.logThreshold;
        }

        if (
            utility.requireArgumentIfDefined(
                options?.reportListener,
                'options.reportListener',
                'function'
            )
        ) {
            this._reportListener = options.reportListener;
        }

        if (
            utility.requireArgumentIfDefined(
                options?.errorListener,
                'options.errorListener',
                'function'
            )
        ) {
            this._errorListener = options.errorListener;
        }
    }

    requestIdleDetectedCallback(callback: IdleDetectedListener): void {
        utility.requireArgument(callback, 'callback', 'function');
        if (this._listeners.size === 0) {
            this._firstCallbackTime = utility.time().perfNow;
        }
        this._listeners.add(callback);
        this._checkIfLoaded();
    }

    private _reportTaskEnded(tasker: TaskerImpl): void {
        // Report only if we have any listeners
        if (this._listeners.size) {
            const snapshot = Object.assign({}, this._taskers.get(tasker));
            this._endedTasks.push(snapshot);
        }
    }

    declareNotifierTaskSingle(name: string): TaskerSingle {
        utility.requireArgument(name, 'name', 'string');
        const tasker = this._addTasker(name, 1);

        const retVal: TaskerSingle = {
            get isBusy() {
                return tasker.isBusy;
            },
            done: () => {
                tasker.done();
                this._taskers.delete(tasker);
            }
        };

        return retVal;
    }

    declareNotifierTaskMulti(name: string, existingBusyCount = 0): TaskerMulti {
        utility.requireArgument(name, 'name', 'string');
        if (existingBusyCount !== undefined) {
            utility.requireArgument(existingBusyCount, 'existingBusyCount', 'number');

            if (!Number.isInteger(existingBusyCount) || existingBusyCount < 0) {
                throw new Error('existingBusyCount accepts only non-negative integers');
            }
        }
        const tasker = this._addTasker(name, existingBusyCount);

        const retVal: TaskerMulti = {
            get isBusy() {
                return tasker.isBusy;
            },
            add: () => {
                if (!tasker.isBusy) {
                    const meta = this._taskers.get(tasker);
                    meta.start = utility.time().perfNow;
                    meta.end = undefined;
                }
                tasker.add();
            },
            done: tasker.done.bind(tasker) // Note: don't delete multi tasker
        };
        return retVal;
    }

    declarePollableTaskMulti(name: string, isBusyChecker: IsBusyChecker): void {
        utility.requireArgument(name, 'name', 'string');
        utility.requireArgument(isBusyChecker, 'isBusyChecker', 'function');

        this._busyCheckers.set(isBusyChecker, name);
    }

    private _addTasker(name: string, existingBusyCount: number): TaskerImpl {
        const tasker = new TaskerImpl(
            name,
            existingBusyCount,
            () => {
                // Done callback
                this._taskers.get(tasker).end = utility.time().perfNow;
                this._reportTaskEnded(tasker);
                this._checkIfLoaded();
            },
            () => {
                // Overdone callback
                this._errorListener?.(`Overdone: ${tasker.name}`);
                utility.noProdThrow(`Tasker '${tasker.name}' is done too many times.`);
            }
        );
        this._taskers.set(tasker, {
            name,
            start: existingBusyCount > 0 ? utility.time().perfNow : undefined,
            end: undefined
        });
        return tasker;
    }

    private _areAllNotifiersIdle(): boolean {
        return !Array.from(this._taskers.keys()).some((t) => t.isBusy);
    }

    private _getFirstBusyPollable(): string {
        const entry = Array.from(this._busyCheckers).find((entry) => {
            const isBusyChecker = entry[0];
            const name = entry[1];
            try {
                return isBusyChecker();
            } catch (e) {
                this._errorListener?.(e, `BusyChecker ${name}`);
                utility.noProdThrow(e);
                return false; // Not busy
            }
        });

        return entry ? entry[1] : undefined;
    }

    private _checkIfLoaded(): void {
        if (!this._listeners.size) {
            // bail out early. We don't have any callbacks queued
            return;
        }

        // We restart the idle timer check logic whenever we get an external
        // trigger that a resource was loaded since that would imply that more
        // work could be queued up and we want to be aware of that
        if (this._timerCheckQueued) {
            clearTimeout(this._timerCheckQueued);
        }

        if (this._areAllNotifiersIdle()) {
            // Enqueue two RAF to make sure nothing got queued in this js cycle
            // note the use of double setTimeout. We want our setTimeout to queue up after
            // any set timeouts queued within micro tasks that may run after processing this
            // xhr response.
            // Here's a sample timeline
            // .....[            Single Event Loop          ].....................................
            // .....[XHR callback][checkIfLoaded][microtasks]-[1st setTimeout]....[2nd setTimeout]
            //                                               ^
            // queue settimeout at the end of event loop --->|
            this._timerCheckQueued = setTimeout(() => {
                this._timerCheckQueued = setTimeout(() => {
                    this._doubleCheck();
                }, 40);
            }, 0);
        }
    }

    private _doubleCheck(): void {
        this._timerCheckQueued = undefined;

        if (this._areAllNotifiersIdle()) {
            const pollableName: string = this._getFirstBusyPollable();
            if (pollableName !== undefined) {
                // we'll have to keep polling until polled operations finish
                this._addToPollableReport(pollableName);
                this._timerCheckQueued = setTimeout(() => {
                    this._doubleCheck();
                }, 15);
            } else {
                // OK!! We are certified idle.
                const maxDuration = utility.time().perfNow - this._firstCallbackTime;

                // Take a snapshot of internal variables so that we don't get confused
                // if tasks are created or listeners or worse, get into an infinite loop
                // if idle is requested
                const listeners = Array.from(this._listeners.values());
                this._listeners.clear();

                this._firstCallbackTime = undefined;
                const endedTasks = this._endedTasks.splice(0);
                const pollableReport = this._pollableReport.splice(0);

                this._notify(listeners, maxDuration, endedTasks, pollableReport);
            }
        }
    }

    private _addToPollableReport(name: string): void {
        const lastEntry: PollableReportEntry = this._pollableReport.length
            ? this._pollableReport[this._pollableReport.length - 1]
            : undefined;

        if (lastEntry?.name === name) {
            lastEntry.count += 1;
        } else {
            this._pollableReport.push({
                name,
                count: 1
            });
        }
    }

    private _notify(
        listeners: IdleDetectedListener[],
        maxDuration: number,
        taskReport: Array<TaskReportEntry>,
        pollableReport: Array<PollableReportEntry>
    ): void {
        if (maxDuration >= this._logThreshold && this._reportListener) {
            const logData: IdleDetectorReport = this._getReport(
                maxDuration,
                listeners.length,
                taskReport,
                pollableReport
            );
            this._reportListener(logData);
        }
        listeners.reverse();

        const tsNow = utility.time().tsNow;
        for (const [index, listener] of listeners.entries()) {
            try {
                listener(tsNow);
            } catch (e) {
                this._errorListener?.(e, `Listener #${listeners.length - 1 - index}`);
                utility.noProdThrow(e);
            }
        }
    }

    getReport(): IdleDetectorReport {
        const perfNow = utility.time().perfNow;
        const maxDuration =
            this._firstCallbackTime > 0 ? perfNow - this._firstCallbackTime : undefined;

        const onGoingTasks: Array<TaskReportEntry> = Array.from(this._taskers.entries())
            .filter((e) => e[0].isBusy)
            .map((e) => ({
                name: e[1].name,
                start: e[1].start,
                end: perfNow
            }));
        return this._getReport(
            maxDuration,
            this._listeners.size,
            this._endedTasks.concat(onGoingTasks),
            this._pollableReport
        );
    }

    private _getReport(
        maxDuration: number,
        listenerCount: number,
        taskReport: Array<TaskReportEntry>,
        pollableReport: Array<PollableReportEntry>
    ): IdleDetectorReport {
        const sortedTaskers = taskReport
            .map((t) => ({
                name: t.name,
                duration: t.end - t.start
            }))
            .sort((a, b) => b.duration - a.duration);
        return {
            logThreshold: this._logThreshold,
            maxDuration,
            listenerCount,
            pollableCount: this._busyCheckers.size,
            busyTasks: sortedTaskers.map((t) => t.name),
            busyDurations: sortedTaskers.map((t) => t.duration),
            pollables: pollableReport.map((p) => p.name),
            pollCounts: pollableReport.map((p) => p.count)
        };
    }
}
