import { RootActivity, Schema } from '../interfaces';
import { ActivityDetail } from '../interfaces/ActivityDetail';
import { ActivityImpl, onActivityError, onActivityStop } from './ActivityImpl';
import { SchematizedData, utility } from 'o11y/shared';

export class RootActivityImpl extends ActivityImpl implements RootActivity {
    constructor(
        name: string,
        onStopped: onActivityStop,
        onError: onActivityError,
        rootId?: string,
        _isSampled?: boolean
    ) {
        super(
            name,
            undefined,
            onStopped,
            onError,
            undefined,
            rootId || utility.generateUniqueId(32),
            _isSampled
        );
    }

    private _preRootId: string;
    get preRootId(): string {
        return this._preRootId;
    }
    set preRootId(value: string) {
        this._preRootId = value;
    }

    protected _getDetail(userSchema: Schema, userData: SchematizedData): ActivityDetail {
        const detail = super._getDetail(userSchema, userData);
        detail.isRoot = true;
        detail.preRootId = this.preRootId;
        return detail;
    }
}
