import { IType, Schema, SchemaTokens } from '../interfaces';
import {
    PbjsNestedObjectDescriptor,
    SchematizedData,
    SchematizedDataValue,
    schemaUtil,
    utility
} from 'o11y/shared';

// These are hard-coded limits at the app-level. They may be overridden with a corresponding
// option at the field level in the schema metadata.
export const maxStringLengthAppLimit = 10000; // For strings
export const maxItemCountAppLimit = 10000; // For arrays

const minUnsigned = 0;
const maxFourBytes = 2147483647;
const minFourBytes = -2147483648;
const maxFourBytesUnsigned = 4294967295;
const maxEightBytes = 9223372036854776000;
const minEightBytes = -9223372036854776001;
const maxEightBytesUnsigned = 18446744073709552000;

export const enum ErrorCode {
    notAnError = 0, // To ensure truthy-checks work for proper error codes.
    arrayExpected,
    incorrectType,
    infiniteNumber,
    numberOutOfRange,
    malformedBytesArray,
    stringLengthOverflowAppLimit,
    itemCountOverflowAppLimit,
    stringLengthOverflow,
    stringLengthUnderflow,
    itemCountOverflow,
    itemCountUnderflow,
    integerExpected,
    invalidItemValue
}

export class ValidationEntry {
    constructor(
        public errorCode: ErrorCode,
        public fields: string[],
        public expected?: string,
        public received?: string
    ) {}

    asMessage(schemaId: string): string {
        const key: string =
            this.fields[0] +
            this.fields
                .slice(1)
                .reduce(
                    (prev, current) =>
                        Number(current) >= 0 ? `${prev}[${current}]` : `${prev}.${current}`,
                    ''
                );
        let msg: string;

        switch (this.errorCode) {
            case ErrorCode.arrayExpected:
                msg = 'Repeated field must be an array';
                break;
            case ErrorCode.incorrectType:
                msg = `Expected type ${this.expected} but received type ${this.received}`;
                break;
            case ErrorCode.infiniteNumber:
                msg = 'Value must be finite';
                break;
            case ErrorCode.numberOutOfRange:
                msg = 'Value is out of range for its type';
                break;
            case ErrorCode.malformedBytesArray:
                msg = 'Bytes array is malformed';
                break;
            case ErrorCode.stringLengthOverflowAppLimit:
                msg = 'Exceeded app limit for maximum string length';
                break;
            case ErrorCode.itemCountOverflowAppLimit:
                msg = 'Exceeded app limit for item count';
                break;
            case ErrorCode.integerExpected:
                msg = 'Value must be an integer';
                break;
            case ErrorCode.invalidItemValue:
                msg = 'Values like null or undefined are not allowed for items in repeated fields';
                break;
            // TODO: The following should be used when we start validating against schema metadata
            // case ErrorCode.stringLengthOverflow:
            //     msg = 'Exceeded maximum string length';
            //     break;
            // case ErrorCode.stringLengthUnderflow:
            //     msg = 'Failed minimum string length';
            //     break;
            // case ErrorCode.itemCountOverflow:
            //     msg = 'Exceeded maximum item count';
            //     break;
            // case ErrorCode.itemCountUnderflow:
            //     msg = 'Failed minimum item count';
            //     break;
            default:
                msg = `Unknown error code: ${this.errorCode}`;
                break;
        }

        return `Schema ${schemaId} on field "${key}": ${msg}`;
    }
}

type NumberRangeOptions = {
    min: number;
    max: number;
    isInt: boolean;
    isItem: boolean;
};

class LogValidator {
    public validate(schema: Schema, data: SchematizedData, noThrow = false): ValidationEntry[] {
        const schemaTokens: SchemaTokens = schemaUtil.checkSchema(schema);
        utility.requireArgument(data, 'data', 'object');

        const schemaId: string = schemaUtil.getSchemaId(schema);

        const errorInfos = new Array<ValidationEntry>();

        const nestedSchema: PbjsNestedObjectDescriptor = schemaUtil.getTypes(schema);
        this._validateFields(
            errorInfos,
            [],
            data,
            nestedSchema[schemaTokens.message] as IType,
            nestedSchema,
            schemaId
        );

        if (errorInfos.length && !noThrow) {
            throw new Error(errorInfos[0].asMessage(schemaId));
        }

        return errorInfos;
    }

    private _validateFields(
        errorInfos: ValidationEntry[],
        fieldNames: string[],
        data: SchematizedData,
        message: IType,
        descriptor: PbjsNestedObjectDescriptor,
        schemaId: string
    ): void {
        const oneofs = message.oneofs || {};
        const fields = message.fields || {};
        for (const key in data) {
            const value = data[key];
            // if it's null or undefined we don't need to match
            if (value !== undefined && value !== null) {
                const mft: (fieldType: string, isRepeated?: boolean, isItem?: boolean) => void = (
                    fieldType,
                    isRepeated,
                    isItem
                ) =>
                    this._matchFieldTypes(
                        errorInfos,
                        [...fieldNames, key],
                        value,
                        fieldType,
                        descriptor,
                        schemaId,
                        key,
                        isRepeated,
                        isItem
                    );

                if (oneofs[key]) {
                    mft(fields[oneofs[key].oneof[0]].type);
                } else if (fields[key]) {
                    mft(fields[key].type, fields[key].rule === 'repeated');
                }
                // if the field is not present in the schema then we ignore it and will drop it later
            }
        }
    }

    private _matchFieldTypes(
        errorInfos: ValidationEntry[],
        fieldNames: string[],
        fieldDataValue: SchematizedDataValue,
        protobufType: string,
        descriptor: PbjsNestedObjectDescriptor,
        schemaId: string,
        key: string,
        isRepeated: boolean,
        isItem: boolean // true if inside a repeated field
    ): void {
        let errorCode: ErrorCode;

        if (isRepeated) {
            if (Array.isArray(fieldDataValue)) {
                if (fieldDataValue.length > maxItemCountAppLimit) {
                    errorCode = ErrorCode.itemCountOverflowAppLimit;
                }

                fieldDataValue.forEach((value, index: number) => {
                    // if it's repeated they should all be the same type so we can just run it as is
                    this._matchFieldTypes(
                        errorInfos,
                        [...fieldNames, index.toString()],
                        value,
                        protobufType,
                        descriptor,
                        schemaId,
                        key,
                        undefined, // rule
                        true // isItem
                    );
                });
            } else {
                errorCode = ErrorCode.arrayExpected;
            }
        } else if (fieldDataValue == null || fieldDataValue === undefined) {
            if (isItem) {
                // Null and undefined are allowed unless they're in an array
                errorCode = ErrorCode.invalidItemValue;
            }
        } else {
            let numOpts: NumberRangeOptions;
            let javaScriptType: string;
            switch (protobufType) {
                case 'string':
                    javaScriptType = 'string';
                    if (fieldDataValue.length > maxStringLengthAppLimit) {
                        errorCode = ErrorCode.stringLengthOverflowAppLimit;
                    }
                    break;
                case 'bytes':
                    javaScriptType = 'object';
                    if (!(fieldDataValue instanceof Uint8Array)) {
                        errorCode = ErrorCode.malformedBytesArray;
                    }
                    break;
                case 'bool':
                    javaScriptType = 'boolean';
                    break;
                case 'uint32':
                    javaScriptType = 'number';
                    numOpts = {
                        min: minUnsigned,
                        max: maxFourBytesUnsigned,
                        isInt: true,
                        isItem
                    };
                    break;
                case 'int32':
                case 'sint32':
                case 'fixed32':
                case 'sfixed32':
                    javaScriptType = 'number';
                    numOpts = {
                        min: minFourBytes,
                        max: maxFourBytes,
                        isInt: true,
                        isItem
                    };
                    break;
                case 'uint64':
                    javaScriptType = 'number';
                    numOpts = {
                        min: minUnsigned,
                        max: maxEightBytesUnsigned,
                        isInt: true,
                        isItem
                    };
                    break;
                case 'fixed64':
                case 'sfixed64':
                case 'int64':
                case 'sint64':
                    javaScriptType = 'number';
                    numOpts = {
                        min: minEightBytes,
                        max: maxEightBytes,
                        isInt: true,
                        isItem
                    };
                    break;
                case 'double':
                case 'float':
                    javaScriptType = 'number';
                    numOpts = {
                        min: Number.NEGATIVE_INFINITY,
                        max: Number.POSITIVE_INFINITY,
                        isInt: false,
                        isItem
                    };
                    break;
                default:
                    if (new Set(Object.keys(descriptor)).has(protobufType)) {
                        //now we check this schema
                        this._validateFields(
                            errorInfos,
                            fieldNames,
                            fieldDataValue,
                            descriptor[protobufType] as IType,
                            descriptor,
                            schemaId
                        );
                        javaScriptType = 'object';
                    }
                    break;
            }

            if (typeof fieldDataValue !== javaScriptType) {
                errorInfos.push(
                    new ValidationEntry(
                        ErrorCode.incorrectType,
                        fieldNames,
                        protobufType,
                        typeof fieldDataValue
                    )
                );
            } else if (numOpts) {
                if (numOpts.isInt && !Number.isFinite(fieldDataValue)) {
                    errorCode = ErrorCode.infiniteNumber;
                } else if (numOpts.isInt && !Number.isInteger(fieldDataValue)) {
                    errorCode = ErrorCode.integerExpected;
                } else if (
                    !(!numOpts.isInt && Number.isNaN(fieldDataValue)) &&
                    !(fieldDataValue >= numOpts.min && fieldDataValue <= numOpts.max)
                ) {
                    errorCode = ErrorCode.numberOutOfRange;
                }
            }
        }
        if (errorCode) {
            errorInfos.push(new ValidationEntry(errorCode, fieldNames));
        }
    }
}

export const logValidator = new LogValidator();
