import { utility } from 'o11y/shared';
import { AllowedOrigins, Communicator } from '../interfaces';
import { SenderLogMessage, SenderMetricsMessage } from '../interfaces-private';
import { MESSAGE_FROM_RECEIVER } from './ReceiverConnector';

export const MESSAGE_FROM_SENDER = 'o11y:sender';
export const MESSAGE_TYPE_HANDSHAKE = 'handshake';
export const MESSAGE_TYPE_LOG = 'log';
export const MESSAGE_TYPE_METRICS = 'metrics';

export type HandshakeData = {
    from: string;
    type: string;
};

export type MessageChannelData = {
    // No need for from here.
    type: string;
    data: SenderLogMessage | SenderMetricsMessage;
};

// This is typically the iframe
export class SenderConnector {
    private readonly _messageEventHandlerFunc = this._messageEventHandler.bind(this);
    private _otherPort: MessagePort;
    private _handshakeComplete = false;
    private _logBuffer: SenderLogMessage[] = [];
    private _metricsBuffer: SenderMetricsMessage[] = [];

    constructor(
        private readonly _self: Communicator,
        private readonly _allowedOrigins?: AllowedOrigins
    ) {
        utility.requireArgument(this._self, '_self', 'object');
        utility.requireArgumentIfDefined(_allowedOrigins, 'allowedOrigins', Array);

        // Start listening to handshake return
        this._self.addEventListener('message', this._messageEventHandlerFunc);
    }

    private _messageEventHandler(e: MessageEvent<HandshakeData>): void {
        // When the receiever sends the handshake, it's ready to receive data
        if (
            e.data.from === MESSAGE_FROM_RECEIVER &&
            e.data.type === MESSAGE_TYPE_HANDSHAKE &&
            e.ports[0] instanceof MessagePort &&
            utility.isAllowedOrigin(e.origin, this._allowedOrigins)
        ) {
            this._handshakeComplete = true;
            this._self.removeEventListener('message', this._messageEventHandlerFunc);
            this._otherPort = e.ports[0];
            this._emptyBuffers();
        }
    }

    public sendLog(log: SenderLogMessage): void {
        if (!this._handshakeComplete) {
            this._logBuffer.push(log);
        } else {
            this._otherPort.postMessage({
                type: MESSAGE_TYPE_LOG,
                data: log
            });
        }
    }

    public sendMetrics(metrics: SenderMetricsMessage): void {
        if (!this._handshakeComplete) {
            this._metricsBuffer.push(metrics);
        } else {
            this._otherPort.postMessage({
                type: MESSAGE_TYPE_METRICS,
                data: metrics
            });
        }
    }

    private _emptyBuffers(): void {
        this._logBuffer.forEach((log) => this.sendLog(log));
        this._metricsBuffer.forEach((metrics) => this.sendMetrics(metrics));
        this._logBuffer = undefined;
        this._metricsBuffer = undefined;
    }
}
