import { SchematizedData, metricsUtility, utility } from 'o11y/shared';
import { SenderLogMessage, SenderMetricsMessage } from '../interfaces-private';
import {
    LogCollector,
    LogMeta,
    MetricsCollector,
    MetricsExtractorMethods,
    Schema
} from '../interfaces';
import { SenderConnector } from '../library/SenderConnector';

export const DEFAULT_POLL_INTERVAL = 1000;

export class SenderCollector implements LogCollector, MetricsCollector {
    private readonly _pollInterval: number = DEFAULT_POLL_INTERVAL;
    private _metricsExtractors: MetricsExtractorMethods;

    constructor(private readonly _connector: SenderConnector, _pollInterval?: number) {
        utility.requireArgument(_connector, '_connector', 'object');
        if (
            utility.requireArgumentIfDefined(_pollInterval, '_pollInterval', 'number') &&
            _pollInterval > 0
        ) {
            this._pollInterval = _pollInterval;
        }
    }

    collect(schema: Schema, data: SchematizedData, logMeta: LogMeta): void {
        const msg: SenderLogMessage = {
            schema,
            data,
            logMeta
        };

        this._connector.sendLog(msg);
    }

    receiveMetricsExtractors(extractors: MetricsExtractorMethods): void {
        this._metricsExtractors = extractors;

        // Send metrics immediately after receiving extractors
        this._collectAndSendMetrics();
        setInterval(this._collectAndSendMetrics.bind(this), this._pollInterval);
    }

    private _collectAndSendMetrics(): void {
        const metrics: SenderMetricsMessage = {
            upCounters: metricsUtility.getUpCounters(this._metricsExtractors.getAllUpCounters()),
            valueRecorders: metricsUtility.getValueRecorders(
                this._metricsExtractors.getAllValueRecorders()
            ),
            bucketHistograms: metricsUtility.getBucketHistograms(
                this._metricsExtractors.getAllBucketHistograms()
            )
        };

        if (
            metrics.upCounters.length ||
            metrics.valueRecorders.length ||
            metrics.bucketHistograms.length
        ) {
            this._connector.sendMetrics(metrics);
        }
    }
}
