import { ActivityDetail, RootActivity } from '../interfaces';
import { ActivityData } from '../interfaces-private';
import { discarded, onActivityStop } from './ActivityImpl';
import { InstrumentationImpl } from './InstrumentationImpl';
import { RootActivityImpl } from './RootActivityImpl';
import { rootActivityNoOp } from './RootActivityNoOp';
import { utility } from 'o11y/shared';
import { MetricsImpl } from './MetricsImpl';
import { NextgenImpl } from './NextgenImpl';

export class AppInstrumentationImpl extends InstrumentationImpl {
    private _singleRootActivity: RootActivityImpl; // Not used when _allowMulti = true

    private readonly _onRootActivityStoppedCallback: onActivityStop =
        this._handleRootActivityStop.bind(this);

    constructor(_nextGen: NextgenImpl, _name: string, private readonly _allowMulti: boolean) {
        super(_nextGen, _name);
    }

    startRootActivity(name: string, rootId?: string, isSampled?: boolean): RootActivity {
        return this._safety.tryCatchReturn(() => {
            utility.requireArgument(name, 'name');

            const newRootActivity = new RootActivityImpl(
                name,
                this._onRootActivityStoppedCallback,
                this._onActivityErrorCallback,
                rootId,
                isSampled
            );

            if (!this._allowMulti) {
                if (this._singleRootActivity && !this._singleRootActivity.isStopped) {
                    const preRootId = this._singleRootActivity.getId();
                    this._singleRootActivity.terminate();
                    newRootActivity.preRootId = preRootId;
                }
                this._singleRootActivity = newRootActivity;
            }

            return newRootActivity as RootActivity;
        }, rootActivityNoOp);
    }

    private _handleRootActivityStop(detail: ActivityDetail): void {
        if (detail.stopReason === discarded) {
            return;
        }

        const activityData: ActivityData = this._getActivityData(detail);

        this._logActivity(activityData, detail.startTimestamp);
        this._singleRootActivity = undefined;
    }

    protected _getActivityData(detail: ActivityDetail): ActivityData {
        const activityData: ActivityData = super._getActivityData(detail);
        activityData.isRoot = detail.isRoot;
        activityData.preRootId = detail.preRootId;
        return activityData;
    }

    getSingleRootActivityId(): string {
        return this._singleRootActivity?.getId();
    }

    isSingleRootActivitySampled(): boolean {
        return this._singleRootActivity?.getIsSampled();
    }

    protected _initMetrics(): MetricsImpl {
        return new MetricsImpl(this.name, () => this.name);
    }
}
