import { utility } from 'o11y/shared';
import { Communicator } from '../interfaces';
import { SenderLogMessage, SenderMetricsMessage } from '../interfaces-private';
import {
    MESSAGE_TYPE_LOG,
    MESSAGE_TYPE_METRICS,
    MESSAGE_TYPE_HANDSHAKE,
    MessageChannelData
} from './SenderConnector';

export const MESSAGE_FROM_RECEIVER = 'o11y:receiver';

export type LogCallback = (log: SenderLogMessage) => void;
export type MetricsCallback = (metric: SenderMetricsMessage) => void;

export type CommErrorInfo = {
    data: unknown;
};
// This callback will be called if we receive data that can't be serialized.
export type CommErrorCallback = () => void;

enum CommState {
    Unknown,
    Opening
}

type CommStatus = {
    state: CommState;
    port: MessagePort;
};

export class ReceiverConnector {
    private _senders: Map<Communicator, CommStatus> = new Map();

    constructor(
        private readonly _logCallback: LogCallback,
        private readonly _metricsCallback: MetricsCallback,
        private readonly _commErrorCallback: CommErrorCallback
    ) {
        utility.requireArgument(this._logCallback, '_logCallback', 'function');
        utility.requireArgument(this._metricsCallback, '_metricsCallback', 'function');
        utility.requireArgument(this._commErrorCallback, '_commErrorCallback', 'function');
    }

    public openComms(sender: Communicator): void {
        utility.requireArgument(sender, 'sender', 'object');

        if (!this._senders.has(sender)) {
            const messageChannel = new MessageChannel();

            const status: CommStatus = {
                state: CommState.Opening,
                port: messageChannel.port1
            };
            this._senders.set(sender, status);

            status.port.onmessage = this._messagePortEventHandler.bind(this);
            // According to the docs, this error occurs on the receiving end.
            status.port.onmessageerror = this._messagePortErrorHandler.bind(this);

            sender.postMessage(
                {
                    from: MESSAGE_FROM_RECEIVER,
                    type: MESSAGE_TYPE_HANDSHAKE
                },
                '*',
                [messageChannel.port2]
            );
        }
    }

    public closeComms(sender: Communicator): void {
        const status = this._senders.get(sender);
        if (status) {
            status.port.onmessage = undefined;
            status.port.onmessageerror = undefined;
            this._senders.delete(sender);
        }
    }

    private _messagePortEventHandler(e: MessageEvent<MessageChannelData>): void {
        if (e.data.type === MESSAGE_TYPE_LOG) {
            this._logCallback(e.data.data as SenderLogMessage);
        } else if (e.data.type === MESSAGE_TYPE_METRICS) {
            this._metricsCallback(e.data.data as SenderMetricsMessage);
        }
    }

    private _messagePortErrorHandler(): void {
        // Not passing any error info for now
        this._commErrorCallback();
    }
}
