import { Instrumentation } from '../interfaces';
import { utility } from 'o11y/shared';

const pathLimit = 5;

export class AutomaticClickTracker {
    private _ignoredEvent: Event;
    private _isActive = false;
    private readonly _boundClickListener = this._clickListener.bind(this);

    constructor(private readonly _instr: Instrumentation, private readonly _doc: Document) {
        utility.requireArgument(_instr, '_instr');
        utility.requireArgument(_doc, '_doc');
    }

    activate(): void {
        if (!this._isActive) {
            this._doc.addEventListener('click', this._boundClickListener, true);
            this._isActive = true;
        }
    }

    deactivate(): void {
        if (this._isActive) {
            this._doc.removeEventListener('click', this._boundClickListener, true);
            this._isActive = false;
        }
    }

    markEventHandled(event: MouseEvent): void {
        this._ignoredEvent = event;
    }

    private _clickListener(event: MouseEvent): void {
        let eventTargets: EventTarget[] = event.composedPath?.();
        if (!eventTargets || !eventTargets.length) {
            /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
            eventTargets = (event as any).path;
        }
        const handledBy: HTMLElement = this._getClickableElement(eventTargets);

        if (handledBy) {
            // Immediate
            setTimeout(() => {
                if (event !== this._ignoredEvent) {
                    this._instr.domEvent(event, handledBy, undefined, undefined, {
                        _auto: true
                    });
                }
            });
        }
    }

    private _getClickableElement(path: EventTarget[]): HTMLElement {
        const limit = path ? Math.min(path.length, pathLimit) : 0;
        for (let x = 0; x < limit; x += 1) {
            const currentElement = path[x] as HTMLElement;
            const tagName = currentElement.tagName?.toLowerCase();
            if (tagName === 'a' || tagName === 'button') {
                return currentElement;
            }
            if (tagName === 'input') {
                const inputElement = currentElement as HTMLInputElement;
                if (inputElement.type?.toLowerCase() === 'button') {
                    return inputElement;
                }
            }
        }
        return undefined;
    }
}
