import { metricsUtility } from 'o11y/shared';
import { BucketMetric, Metric, MetricsReporter } from '../interfaces';
import {
    BucketHistogram,
    SenderMetricsMessage,
    UpCounter,
    ValueRecorder
} from '../interfaces-private';
import { BucketHistogramImpl } from './BucketHistogramImpl';
import { UpCounterImpl } from './UpCounterImpl';
import { ValueRecorderImpl } from './ValueRecorderImpl';

export class MetricsStore implements MetricsReporter {
    private _upCounters: UpCounterImpl[] = [];
    private _valueRecorders: ValueRecorderImpl[] = [];
    private _bucketHistograms: BucketHistogramImpl[] = [];

    add(senderMetrics: SenderMetricsMessage): void {
        if (senderMetrics.upCounters?.length) {
            this._upCounters.push(
                ...senderMetrics.upCounters.map(
                    (it: UpCounter) =>
                        new UpCounterImpl(
                            it.name,
                            it.ownerName,
                            it.ownerAppName,
                            metricsUtility.getMetricsTags(it.tags),
                            it.createdTimestamp,
                            it.lastUpdatedTimestamp,
                            it.value
                        )
                )
            );
        }

        if (senderMetrics.valueRecorders?.length) {
            this._valueRecorders.push(
                ...senderMetrics.valueRecorders.map(
                    (it: ValueRecorder) =>
                        new ValueRecorderImpl(
                            it.name,
                            it.ownerName,
                            it.ownerAppName,
                            metricsUtility.getMetricsTags(it.tags),
                            it.createdTimestamp,
                            it.lastUpdatedTimestamp,
                            it.values
                        )
                )
            );
        }

        if (senderMetrics.bucketHistograms?.length) {
            this._bucketHistograms.push(
                ...senderMetrics.bucketHistograms.map(
                    (it: BucketHistogram) =>
                        new BucketHistogramImpl(
                            it.name,
                            it.ownerName,
                            it.ownerAppName,
                            metricsUtility.getMetricsTags(it.tags),
                            it.buckets,
                            it.createdTimestamp,
                            it.lastUpdatedTimestamp,
                            it.values
                        )
                )
            );
        }
    }

    getUpCounters(): Metric<number>[] {
        // Remove any metrics that have been reset from before
        this._upCounters = this._upCounters.filter((i) => i.getLastUpdatedOn() !== undefined);
        return this._upCounters;
    }
    getValueRecorders(): Metric<number[]>[] {
        // Remove any metrics that have been reset from before
        this._valueRecorders = this._valueRecorders.filter(
            (i) => i.getLastUpdatedOn() !== undefined
        );
        return this._valueRecorders;
    }
    getBucketHistograms(): BucketMetric<number[]>[] {
        // Remove any metrics that have been reset from before
        this._bucketHistograms = this._bucketHistograms.filter(
            (i) => i.getLastUpdatedOn() !== undefined
        );
        return this._bucketHistograms;
    }
}
