import {
    BucketMetric,
    BufferedLog,
    IdleDetector,
    Instrumentation,
    InstrumentationContext,
    InstrumentedAppMethods,
    InstrumentedAppOptions,
    LogCollector,
    LogCollectorOptions,
    LogMeta,
    Metric,
    MetricsCollector,
    Nextgen,
    OperationMode,
    OperationModeDetector,
    PayloadProvider,
    Schema,
    SchematizedPayload
} from '../interfaces';
import { SenderLogMessage, SenderMetricsMessage } from 'src/interfaces-private';
import { AppInstrumentationImpl } from './AppInstrumentationImpl';
import { AutomaticClickTracker } from './AutomaticClickTracker';
import { InstrumentationImpl } from './InstrumentationImpl';
import { Tracing } from './Tracing';
import { LogPromptListener, SchematizedData, schemaUtil, utility } from 'o11y/shared';
import { SenderConnector } from './SenderConnector';
import { MetricsStore } from './MetricsStore';
import { SenderCollector } from '../sender_collector/SenderCollector';
import { ReceiverConnector } from './ReceiverConnector';

const maxInstrumentationLength = 80;
const truncatedText = '_CUT';
const maxInstrumentationLengthBeforeTruncated = maxInstrumentationLength - truncatedText.length;

// eslint-disable-next-line @typescript-eslint/no-empty-function
function noOpFunc(): void {}

export class NextgenImpl implements Nextgen {
    private readonly _logCollectors = new Set<LogCollector>();
    private readonly _instruments = new Map<string, InstrumentationImpl | AppInstrumentationImpl>();
    private readonly _seqBySchema = new Map<string, number>();
    private _sequence = 0;
    private _appInstr: AppInstrumentationImpl;
    private _autoClickTracker: AutomaticClickTracker;
    private _metricsCollector: MetricsCollector;
    private _appPayloadProvider: PayloadProvider;
    private _pagePayloadProvider: PayloadProvider;
    private _forceDisabledLogCollectors = new Set<LogCollector>();
    private _logCollectorFailures = new Map<LogCollector, number>();
    private static _collectorFailureLimit = 5;
    private _isBufferingEnabled = false;
    private _buffer: BufferedLog[] = [];
    private readonly _logCollectionListeners = new Set<LogPromptListener>();
    private _clientSessionId: string;
    private _opMode: OperationMode = 'default';
    private _isSender = false;
    private _isReceiver = false;
    private _metricsStore: MetricsStore;
    private _receiverConnector: ReceiverConnector;

    get pagePayloadProvider(): PayloadProvider {
        return this._pagePayloadProvider;
    }
    set pagePayloadProvider(provider: PayloadProvider) {
        this._pagePayloadProvider = provider;
    }

    get appPayloadProvider(): PayloadProvider {
        return this._appPayloadProvider;
    }
    set appPayloadProvider(provider: PayloadProvider) {
        this._appPayloadProvider = provider;
    }

    constructor(private readonly _idleDetector: IdleDetector) {}

    registerApp(name: string, options?: InstrumentedAppOptions): InstrumentedAppMethods {
        utility.requireArgument(name, 'name', 'string');
        if (this._appInstr) {
            throw new Error('An app has already been registered with instrumentation.');
        }
        const instr = this._instruments.get(name);
        if (instr) {
            throw new Error(`The instrumentation name ${name} is already taken`);
        }

        this._initOpMode(options?.operationMode);
        this._isSender = this._opMode === 'sender';
        this._isReceiver = this._opMode === 'receiver';

        this._clientSessionId = options?.clientSessionId?.toString() || utility.generateUniqueId();
        this._appInstr = new AppInstrumentationImpl(
            this,
            name,
            options?.allowMultipleRootActivities
        );
        this._instruments.set(name, this._appInstr);

        if (this._isSender) {
            const senderCollector = new SenderCollector(
                new SenderConnector(window, options?.allowedReceiverOrigins),
                options?.senderMetricsPollingInterval
            );
            this.registerLogCollector(senderCollector);
            this.registerMetricsCollector(senderCollector);
        } else if (this._isReceiver) {
            this._metricsStore = new MetricsStore();

            this._receiverConnector = new ReceiverConnector(
                (msg: SenderLogMessage) => {
                    msg.logMeta.receiverClientSessionId = this._clientSessionId;
                    msg.logMeta.receiverRootId = this._appInstr.getSingleRootActivityId();

                    this._multiplex(msg.schema, msg.data, msg.logMeta);
                },
                (msg: SenderMetricsMessage) => {
                    this._metricsStore.add(msg);
                },
                () => {
                    this._appInstr.incrementCounter('o11y-error-receiver', 1, true);
                }
            );
        }

        // Skip buffering if sender mode is enabled
        this._isBufferingEnabled = !this._isSender && options?.enableBuffering;

        const tracing = new Tracing(this.getInstrumentation('Network'), this._idleDetector);

        return {
            log: this._appInstr.log.bind(this._appInstr),
            error: this._appInstr.error.bind(this._appInstr),
            startActivity: this._appInstr.startActivity.bind(this._appInstr),
            domEvent: this._appInstr.domEvent.bind(this._appInstr),
            incrementCounter: this._appInstr.incrementCounter.bind(this._appInstr),
            trackValue: this._appInstr.trackValue.bind(this._appInstr),
            bucketValue: this._appInstr.bucketValue.bind(this._appInstr),
            networkInstrumentation: tracing.networkInstrumentation.bind(tracing),
            registerForLogPrompt: this._appInstr.registerForLogPrompt.bind(this._appInstr),
            activity: this._appInstr.activity.bind(this._appInstr),
            activityAsync: this._appInstr.activityAsync.bind(this._appInstr),

            startRootActivity: this._appInstr.startRootActivity.bind(this._appInstr),
            registerLogCollector: this._isSender ? noOpFunc : this.registerLogCollector.bind(this),
            registerMetricsCollector: this._isSender
                ? noOpFunc
                : this.registerMetricsCollector.bind(this),
            activateClickTracker: this.activateClickTracker.bind(this),
            deactivateClickTracker: this.deactivateClickTracker.bind(this),
            disableBuffering: this.disableBuffering.bind(this),
            promptLogCollection: this.promptLogCollection.bind(this),
            getClientSessionId: this.getClientSessionId.bind(this),

            getOperationMode: this.getOperationMode.bind(this),
            startReceiving: this._isReceiver
                ? this._receiverConnector.openComms.bind(this._receiverConnector)
                : noOpFunc,
            stopReceiving: this._isReceiver
                ? this._receiverConnector.closeComms.bind(this._receiverConnector)
                : noOpFunc
        };
    }

    getInstrumentation(name: string): Instrumentation {
        utility.requireArgument(name, 'name', 'string');
        if (name.length > maxInstrumentationLength) {
            // don't throw if it's too long. just add truncated suffix, but stay within max length
            name = name.substring(0, maxInstrumentationLengthBeforeTruncated) + truncatedText;
        }
        let instr: InstrumentationImpl | AppInstrumentationImpl = this._instruments.get(name);
        if (!instr) {
            instr = new InstrumentationImpl(this, name);
            this._instruments.set(name, instr);
        } else if (instr === this._appInstr) {
            throw new Error(`The instrumentation name ${name} is being used by the app.`);
        }
        return instr;
    }

    get appName(): string {
        return this._appInstr?.name;
    }

    // This is an internal method that needs to be public
    addLog(
        loggerName: string,
        schema: Schema,
        data: SchematizedData,
        timestamp: number,
        rootId: string,
        pagePayload: SchematizedPayload,
        appPayload: SchematizedPayload
    ): number {
        this._sequence += 1;

        // Schema sequences are per user-defined schema, not internal ones such as ActivityData
        const userSchema =
            schemaUtil.isInternal(schema) && data?.userPayload ? data.userPayload.schema : schema;
        const schemaId = schemaUtil.getSchemaId(userSchema);
        let schemaSequence = this._seqBySchema.get(schemaId) || 0;
        schemaSequence += 1;
        this._seqBySchema.set(schemaId, schemaSequence);

        if (!this._isSender) {
            // Make a copy of the data once, lest they get mutated by the caller
            // This is not needed for sender mode since the data is already serialized
            data = utility.clone(data);
        }

        const logMeta: LogMeta = {
            timestamp,
            rootId,
            sequence: this._sequence,
            schemaSequence,
            loggerName,
            // Not cloning the pagePayload and appPayload like we used to because they are
            // provided by trusted entities of the app
            pagePayload: pagePayload || undefined,
            appPayload: appPayload || undefined,
            loggerAppName: this.appName,
            connectionType: utility.getConnectionType(),
            clientSessionId: this._clientSessionId,
            // Calculate age here rather than later to ensure we use the timeOrigin where the log was created (e.g. sender or receiver)
            age: utility.getAge(timestamp)
        };

        if (this._isBufferingEnabled) {
            // Always push to the buffer, even if no eligible collectors
            this._buffer.push({ schema, data, logMeta });
        }

        this._multiplex(schema, data, logMeta);
        return this._sequence;
    }

    private _multiplex(schema: Schema, data: SchematizedData, logMeta: LogMeta): void {
        const eligibleCollectors: LogCollector[] = Array.from(this._logCollectors).filter(
            (collector: LogCollector) =>
                !this._forceDisabledLogCollectors.has(collector) &&
                !collector.getIsCollectDisabled?.()
        );

        if (eligibleCollectors.length > 0) {
            for (const collector of eligibleCollectors) {
                // Not cloning the data for each collector (like we used to) as they are trusted entities of the app
                let failures: number = this._logCollectorFailures.get(collector) || 0;

                try {
                    collector.collect(schema, data, logMeta);
                    // if it got here it means it succeeded/didn't throw so we want to decrement the failure count
                    if (failures > 0) {
                        this._logCollectorFailures.set(collector, failures - 1);
                    }
                } catch (err) {
                    failures += 1;
                    // If a collector fails too much we just disable it
                    if (failures >= NextgenImpl._collectorFailureLimit) {
                        this._forceDisabledLogCollectors.add(collector);
                        if (this._appInstr && (typeof err === 'string' || err instanceof Error)) {
                            this._appInstr.error(err);
                        }
                    } else {
                        this._logCollectorFailures.set(collector, failures);
                        // We can't log an error  here, or the same collector will immediately be called
                        // again, likely resulting in repeat exceptions until the collector gets disabled.
                    }
                }
            }
        }
    }

    getBuffer(): BufferedLog[] {
        return this._buffer;
    }

    disableBuffering(): void {
        this._isBufferingEnabled = false;
        this._buffer = [];
    }

    getClickTracker(): AutomaticClickTracker {
        return this._autoClickTracker;
    }

    getDefaultInstrumentationContext(): InstrumentationContext {
        return {
            rootId: this._appInstr?.getSingleRootActivityId(),
            isRootActivitySampled: this._appInstr?.isSingleRootActivitySampled()
        };
    }

    activateClickTracker(): void {
        if (typeof document !== 'undefined') {
            if (!this._autoClickTracker) {
                this._autoClickTracker = new AutomaticClickTracker(this._appInstr, document);
            }
            this._autoClickTracker.activate();
        }
    }

    deactivateClickTracker(): void {
        if (this._autoClickTracker) {
            this._autoClickTracker.deactivate();
            // In case activateClickTracker is called later with a different instrumentation
            this._autoClickTracker = undefined;
        }
    }

    registerLogCollector(collector: LogCollector, options?: LogCollectorOptions): void {
        utility.requireArgument(collector, 'collector');
        if (this._logCollectors.has(collector)) {
            return;
        }
        this._logCollectors.add(collector);
        this._logCollectorFailures.set(collector, 0);
        if (
            (!collector.getIsCollectDisabled || !collector.getIsCollectDisabled()) &&
            options?.retroactive
        ) {
            for (const log of this._buffer) {
                collector.collect(log.schema, log.data, log.logMeta);
            }
        }
    }

    registerMetricsCollector(collector: MetricsCollector): void {
        utility.requireArgument(collector, 'collector');
        if (this._metricsCollector) {
            throw new Error('A metrics Collector is already registered.');
        }
        this._metricsCollector = collector;
        this._metricsCollector.receiveMetricsExtractors({
            getAllUpCounters: this._getAllUpCounters.bind(this),
            getAllValueRecorders: this._getAllValueRecorders.bind(this),
            getAllBucketHistograms: this._getAllBucketHistograms.bind(this)
        });
    }

    private _getAllUpCounters(): Metric<number>[] {
        const arrOfArrs = Array.from(this._instruments.values()).map((instr) =>
            instr.getUpCounters()
        );
        return arrOfArrs.reduce(
            (pv, cv) => pv.concat(cv),
            this._metricsStore?.getUpCounters() || []
        );
    }

    private _getAllValueRecorders(): Metric<number[]>[] {
        const arrOfArrs = Array.from(this._instruments.values()).map((instr) =>
            instr.getValueRecorders()
        );
        return arrOfArrs.reduce(
            (pv, cv) => pv.concat(cv),
            this._metricsStore?.getValueRecorders() || []
        );
    }

    private _getAllBucketHistograms(): BucketMetric<number[]>[] {
        const arrOfArrs = Array.from(this._instruments.values()).map((instr) =>
            instr.getBucketHistograms()
        );
        return arrOfArrs.reduce(
            (pv, cv) => pv.concat(cv),
            this._metricsStore?.getBucketHistograms() || []
        );
    }

    registerForLogPrompt(listener: LogPromptListener): void {
        this._logCollectionListeners.add(listener);
    }

    promptLogCollection(reason: string): void {
        for (const listener of this._logCollectionListeners.keys()) {
            try {
                listener(reason);
            } catch (err) {
                if (!utility.isProduction) {
                    throw err;
                }
            }
        }
    }

    getClientSessionId(): string {
        return this._clientSessionId;
    }

    private _initOpMode(opMode: OperationMode | OperationModeDetector): void {
        if (typeof opMode === 'function') {
            opMode = opMode();
        }
        if (opMode === undefined || typeof window !== 'object') {
            // Support receiver and sender mode only if window is available, othwerwise fallback to default
            opMode = 'default';
        }

        if (['default', 'receiver', 'sender'].indexOf(opMode) >= 0) {
            this._opMode = opMode;
        } else {
            throw new Error(`Invalid operation mode: ${opMode}`);
        }
    }

    getOperationMode(): OperationMode {
        return this._opMode;
    }
}
