import { Environment, LogCollector, LogMeta, Schema } from '../interfaces';
import { SchematizedData, schemaUtil } from 'o11y/shared';

type ColorMap = Record<string, string>;

const bgColorMap: ColorMap = {
    Error: 'Crimson',
    Activity: 'CadetBlue',
    InstrumentedEvent: 'DarkOliveGreen',
    O11ySample: 'BlueViolet'
};
const colorMap: ColorMap = {
    Error: 'white',
    Activity: 'white',
    InstrumentedEvent: 'white',
    O11ySample: 'white'
};
const defaultColor = 'black';
const defaultBgColor = 'Gainsboro';

export class ConsoleCollector implements LogCollector {
    constructor(environment?: Environment) {
        if (environment) {
            this._log('ConsoleCollector', environment);
        }
    }

    collect(schema: Schema, data: SchematizedData, logMeta: LogMeta): void {
        let label, color, bgColor;
        if (schema.namespace === 'sf.instrumentation') {
            label = schema.name;
            color = colorMap[schema.name] || defaultColor;
            bgColor = bgColorMap[schema.name] || defaultBgColor;
        } else {
            label = schemaUtil.getSchemaId(schema);
        }

        this._log(label, data, logMeta, color, bgColor);
    }

    private _log(
        label: string,
        first?: unknown,
        second?: unknown,
        color: string = defaultColor,
        bgColor: string = defaultBgColor
    ): void {
        const css = `color:${color};background-color:${bgColor}`;
        // eslint-disable-next-line no-console
        console.log(
            `%cO11Y%c ${label}`,
            'color:white;background-color:#FF6600;font-weight:bold',
            css,
            first || '',
            second || ''
        );
    }
}
