import { Metric } from '../interfaces/Metric';
import { ValueRecorder } from '../interfaces/ValueRecorder';
import { MetricsTags, utility } from 'o11y/shared';

export class ValueRecorderImpl implements Metric<number[]>, ValueRecorder {
    private readonly _createdOn: number;
    private _lastUpdatedOn: number;
    private _values = new Array<number>();

    constructor(
        private readonly _name: string,
        private readonly _ownerName: string,
        private readonly _ownerAppName: string,
        private readonly _tags: MetricsTags,
        createdOn?: number,
        lastUpdatedOn?: number,
        values?: number[]
    ) {
        this._createdOn = createdOn === undefined ? utility.time().tsNow : createdOn;
        if (lastUpdatedOn !== undefined) {
            this._lastUpdatedOn = lastUpdatedOn;
        }
        if (values !== undefined) {
            this._values = values;
        }
        // TODO: Add sampling/throttling to put a cap on the size of the array
    }

    getName(): string {
        return this._name;
    }

    getCreatedOn(): number {
        return this._createdOn;
    }

    getLastUpdatedOn(): number {
        return this._lastUpdatedOn;
    }

    getData(): number[] {
        return this.values;
    }

    get values(): number[] {
        return this._values.slice(0, this._values.length);
    }

    record(value: number): void {
        if (typeof value === 'number') {
            this._values.push(value);
            this._lastUpdatedOn = utility.time().tsNow;
            return;
        }

        throw new Error('ValueRecorder can only record numbers.');
    }

    reset(): void {
        this._lastUpdatedOn = undefined;
        this._values = [];
    }

    getOwnerName(): string {
        return this._ownerName;
    }

    getOwnerAppName(): string {
        return this._ownerAppName;
    }

    getTags(): MetricsTags {
        return this._tags;
    }
}
