import {
    activitySchema,
    errorSchema,
    instrumentedEventSchema,
    simpleSchema
} from 'o11y_schema/sf_instrumentation';

import {
    LightningElement,
    LogPromptListener,
    MetricsTags,
    PublicSafety,
    SchematizedData,
    schemaUtil,
    utility
} from 'o11y/shared';

import {
    Activity,
    ActivityApiOptions,
    ActivityDetail,
    ApiOptions,
    BucketMetric,
    DomEventApiOptions,
    Instrumentation,
    InstrumentationContext,
    Metric,
    MetricsReporter,
    PayloadProvider,
    Schema,
    SchematizedPayload
} from '../interfaces';
import { ActivityData, ErrorData, InstrumentedEventData } from '../interfaces-private';

import { ActivityImpl, discarded, onActivityError, onActivityStop } from './ActivityImpl';
import { activityNoOp } from './ActivityNoOp';
import { AutomaticClickTracker } from './AutomaticClickTracker';
import { domEventHelpers } from './DomEventHelpers';
import {
    ErrorCode,
    ValidationEntry,
    logValidator,
    maxItemCountAppLimit,
    maxStringLengthAppLimit
} from './LogValidator';
import { MetricsImpl } from './MetricsImpl';
import { MockBase } from './MockObj';
import { NextgenImpl } from './NextgenImpl';

// @W-10241440, disable timeouts
const maxActivityDurationMsecs: number = undefined;
const reservedMetricCharacters = ['`'];
const reservedMetricNames = {
    internalError: 'o11y-error'
};
const denyListMetricNames = Object.freeze(Array.from(Object.values(reservedMetricNames)));

export class InstrumentationImpl implements Instrumentation, MetricsReporter {
    protected static readonly defaultAppName = 'APP_NOT_REGISTERED';
    private readonly _metrics: MetricsImpl;
    private readonly _onActivityStoppedCallback: onActivityStop =
        this._handleActivityStop.bind(this);
    protected readonly _onActivityErrorCallback: onActivityError =
        this._handleActivityError.bind(this);
    protected readonly _safety: PublicSafety;
    private readonly _simpleTextMaxLength: number;

    constructor(private readonly _nextGen: NextgenImpl, private readonly _name: string) {
        for (const reservedChar of reservedMetricCharacters) {
            if (this._name.indexOf(reservedChar) >= 0) {
                throw new Error(`Name cannot include the reserved character "${reservedChar}"`);
            }
        }

        this._safety = new PublicSafety({
            increment: (increment) => this._incrementError(increment)
        });

        this._metrics = this._initMetrics();

        const simpleTextOptions = schemaUtil.getOptions(simpleSchema, 'Simple', 'text');
        this._simpleTextMaxLength = simpleTextOptions
            ? simpleTextOptions['(meta.max_length)']
            : maxStringLengthAppLimit;
    }

    protected _initMetrics(): MetricsImpl {
        return new MetricsImpl(
            this.name,
            () => this._nextGen.appName || InstrumentationImpl.defaultAppName
        );
    }

    private _incrementError(increment = 1): void {
        // Note: this metric was setting false for hasError until 246.3.x
        this._metrics.incrementCounter(reservedMetricNames.internalError, increment, true);
    }

    get name(): string {
        return this._name;
    }

    /**
     * This function is to allow for convenience calls to our API, where a string can
     * be provided instead of an actual Schema. In that case, we wrap the text in
     * a built-in simple schema.
     * @param userSchemaOrText Schema or string.
     * @param userData
     * @param onlyIfBoth If true, then both the schema and the data must be specified.
     * @returns
     */
    protected _wrapUserPayload(
        userSchemaOrText: Schema | string,
        userData: SchematizedData,
        onlyIfBoth = false
    ): SchematizedPayload {
        let userSchema: Schema;
        if (typeof userSchemaOrText === 'string') {
            userSchema = simpleSchema;
            if (userSchemaOrText) {
                userData = {
                    text: userSchemaOrText.substring(0, this._simpleTextMaxLength)
                };
            } else {
                userData = undefined;
            }
        } else {
            userSchema = userSchemaOrText;
        }

        return schemaUtil.makePayload(userSchema, userData, onlyIfBoth);
    }

    private _getEffectiveRootId(options?: ApiOptions): string {
        return (
            options?.instrumentationContext?.rootId ??
            this._nextGen.getDefaultInstrumentationContext().rootId
        );
    }

    /**
     *
     * @param userSchema The schema of the second argument. Also used as the basis for auto-generated
     * counters for log calls using the same schema.
     * @param userData Optional data that complies with the provided schema
     */
    log(userSchemaOrText: Schema | string, userData?: SchematizedData, options?: ApiOptions): void {
        this._safety.tryCatch(() => {
            const timestamp = utility.time().tsNow;
            utility.requireArgument(userSchemaOrText, 'userSchemaOrText', ['object', 'string']);
            utility.requireArgumentIfDefined(userData, 'userData', 'object');
            utility.requireArgumentIfDefined(options, 'options', 'object');

            const userPayload = this._wrapUserPayload(userSchemaOrText, userData);

            if (userPayload?.payload) {
                this._logInternal(
                    this.name,
                    userPayload.schema,
                    userPayload.payload,
                    timestamp,
                    this._getEffectiveRootId(options)
                );
            }
        });
    }

    protected _logInternal(
        loggerName: string,
        schema: Schema,
        data: SchematizedData,
        timestamp: number,
        rootId?: string
    ): number {
        this._checkInputs(schema, data);
        const appPayload: SchematizedPayload = this._getPayloadFromProvider(
            this._nextGen.appPayloadProvider
        );
        const pagePayload: SchematizedPayload = this._getPayloadFromProvider(
            this._nextGen.pagePayloadProvider
        );

        return this._nextGen.addLog(
            loggerName,
            schema,
            data,
            timestamp,
            rootId,
            pagePayload,
            appPayload
        );
    }

    error(
        error: unknown,
        userSchemaOrText?: Schema | string,
        userData?: SchematizedData,
        options?: ApiOptions
    ): void {
        return this._safety.tryCatch(() => {
            utility.requireArgumentIfDefined(userSchemaOrText, 'userSchemaOrText', [
                'object',
                'string'
            ]);
            utility.requireArgumentIfDefined(userData, 'userData', 'object');
            utility.requireArgumentIfDefined(options, 'options', 'object');

            const rootId: string = this._getEffectiveRootId(options);
            this._errorInternal(error, userSchemaOrText, userData, undefined, rootId);
        });
    }

    protected _errorInternal(
        err: unknown,
        userSchemaOrText?: Schema | string,
        userData?: SchematizedData,
        activityId?: string,
        rootActivityId?: string
    ): number {
        const loggedTimestamp = utility.time().tsNow;

        let error: Error;
        if (err instanceof Error) {
            error = err;
        } else {
            let msg: string;
            if (err === undefined || err === null || typeof err.toString !== 'function') {
                msg = 'UNKNOWN';
            } else {
                msg = err.toString();
            }
            error = new Error(msg);
        }

        const userPayload = this._wrapUserPayload(userSchemaOrText, userData, true);

        const errorData: ErrorData = {
            name: error.name,
            message: error.message,
            stack: error.stack,
            userPayload,
            activityId
        };

        return this._logInternal(
            this.name,
            errorSchema,
            errorData,
            loggedTimestamp,
            rootActivityId
        );
    }

    startActivity(name: string, options?: ApiOptions): Activity {
        return this._safety.tryCatchReturn(() => {
            utility.requireArgument(name, 'name');
            utility.requireArgumentIfDefined(options, 'options', 'object');

            let context: InstrumentationContext;

            const rootId: string = options?.instrumentationContext?.rootId;
            if (rootId) {
                context = {
                    rootId,
                    isRootActivitySampled: options.instrumentationContext.isRootActivitySampled
                };
            } else {
                // By definition, an activity is sampled if root activity is sampled
                context = this._nextGen.getDefaultInstrumentationContext();
            }
            const activity: Activity = new ActivityImpl(
                name,
                context.rootId,
                this._onActivityStoppedCallback,
                this._onActivityErrorCallback,
                maxActivityDurationMsecs,
                undefined,
                context.isRootActivitySampled
            );
            return activity;
        }, activityNoOp);
    }

    protected _getActivityData(detail: ActivityDetail): ActivityData {
        const userPayload = this._wrapUserPayload(detail.userSchemaOrText, detail.userData, true);

        return {
            id: detail.id,
            name: detail.name,
            duration: detail.stopPerfTime - detail.startPerfTime,
            stopReason: detail.stopReason,
            userPayload,
            errorCount: detail.errorCount,
            isSampled: detail.isSampled,
            timerOverridden: detail.timerOverridden
        };
    }

    private _handleActivityStop(detail: ActivityDetail): void {
        if (detail.stopReason === discarded) {
            return;
        }
        const activityData: ActivityData = this._getActivityData(detail);

        this._logActivity(activityData, detail.startTimestamp, detail.rootId);
    }

    protected _logActivity(
        activityData: ActivityData,
        startTimestamp: number,
        rootId?: string
    ): void {
        this._logInternal(this.name, activitySchema, activityData, startTimestamp, rootId);
    }

    private _handleActivityError(error: unknown, detail: ActivityDetail): void {
        this._errorInternal(
            error,
            detail.userSchemaOrText,
            detail.userData,
            detail.id,
            detail.rootId
        );
    }

    private _getPayloadFromProvider(provider: PayloadProvider): SchematizedPayload {
        const payload: SchematizedPayload = provider?.getPayload() || undefined;
        if (payload) {
            this._checkInputs(payload.schema, payload.payload);
        }
        return payload;
    }

    domEvent(
        event: Event,
        handledBy: HTMLElement | LightningElement,
        userSchemaOrText?: Schema | string,
        userData?: SchematizedData,
        options?: DomEventApiOptions
    ): void {
        this._safety.tryCatch(() => {
            const tsNow = utility.time().tsNow;

            utility.requireArgument(event, 'event', [Event, MockBase]);
            // TODO: Using 'object' instead of 'LightningElement' for now (no imports)
            utility.requireArgument(handledBy, 'handledBy', [HTMLElement, 'object']);

            let htmlElement;
            if (handledBy instanceof HTMLElement) {
                htmlElement = handledBy;
            } else {
                handledBy = handledBy as LightningElement;
                // Convenience for LightningElement developers who pass "this" in handledBy.
                // Get the underlying HTML element
                if (handledBy.template?.host instanceof HTMLElement) {
                    htmlElement = handledBy.template.host;
                } else {
                    utility.requireArgument(undefined, 'handledBy');
                }
            }

            const userPayload: SchematizedPayload = this._wrapUserPayload(
                userSchemaOrText,
                userData,
                true
            );

            const rootId: string = this._getEffectiveRootId(options);

            switch (event.type) {
                case 'click': {
                    const mouseEvent = event as MouseEvent;

                    // If the user has called domEvent API on this event explicitly,
                    // then we don't need to log it by default.
                    const acd: AutomaticClickTracker = this._nextGen.getClickTracker();
                    if (acd) {
                        acd.markEventHandled(mouseEvent);
                    }

                    const mouseEventData = domEventHelpers.getMouseEventData(mouseEvent);

                    const instrumentedEvent: InstrumentedEventData =
                        domEventHelpers.getInstrumentedEventData(
                            mouseEventData,
                            htmlElement,
                            userPayload
                        );
                    instrumentedEvent.auto = options?._auto;

                    this._logInternal(
                        this.name,
                        instrumentedEventSchema,
                        instrumentedEvent,
                        tsNow,
                        rootId
                    );
                    break;
                }
                default:
                    throw new Error(utility.notImplemented);
            }
        });
    }

    incrementCounter(
        operation: string,
        increment = 1,
        hasError = false,
        tags: MetricsTags = {}
    ): void {
        this._safety.tryCatch(() => {
            utility.requireArgument(operation, 'operation', 'string');
            utility.checkForDenyListedValues(operation, 'operation', denyListMetricNames);
            utility.checkForReservedCharacters(operation, 'operation', reservedMetricCharacters);
            utility.requireArgument(increment, 'increment', 'number');
            utility.requireArgument(hasError, 'hasError', 'boolean');
            utility.requireArgument(tags, 'tags', 'object');
            Object.entries(tags).forEach((entry) => {
                utility.requireArgument(entry[1], `Tag value for '${entry[0]}'`, [
                    'string',
                    'number',
                    'boolean'
                ]);
            });
            this._metrics.incrementCounter(operation, increment, hasError, tags);
        });
    }

    trackValue(operation: string, value: number, hasError = false, tags: MetricsTags = {}): void {
        this._safety.tryCatch(() => {
            utility.requireArgument(operation, 'operation', 'string');
            utility.checkForDenyListedValues(operation, 'operation', denyListMetricNames);
            utility.checkForReservedCharacters(operation, 'operation', reservedMetricCharacters);
            utility.requireArgument(value, 'value', 'number');
            utility.requireArgument(hasError, 'hasError', 'boolean');
            utility.requireArgument(tags, 'tags', 'object');
            Object.entries(tags).forEach((entry) => {
                utility.requireArgument(entry[1], `Tag value for '${entry[0]}'`, [
                    'string',
                    'number',
                    'boolean'
                ]);
            });

            this._metrics.trackValue(operation, value, hasError, tags);
        });
    }

    bucketValue(
        operation: string,
        value: number,
        buckets: number[] = [],
        hasError = false,
        tags: MetricsTags = {}
    ): void {
        this._safety.tryCatch(() => {
            utility.requireArgument(operation, 'operation', 'string');
            utility.checkForDenyListedValues(operation, 'operation', denyListMetricNames);
            utility.checkForReservedCharacters(operation, 'operation', reservedMetricCharacters);
            utility.requireArgument(value, 'value', 'number');
            utility.requireArgument(buckets, 'buckets', Array);
            Object.entries(buckets).forEach((entry) => {
                utility.requireArgument(entry[1], `Bucket value for '${entry[0]}'`, 'number');
            });
            utility.requireArgument(hasError, 'hasError', 'boolean');
            utility.requireArgument(tags, 'tags', 'object');
            Object.entries(tags).forEach((entry) => {
                utility.requireArgument(entry[1], `Tag value for '${entry[0]}'`, [
                    'string',
                    'number',
                    'boolean'
                ]);
            });

            this._metrics.bucketValue(operation, value, buckets, hasError, tags);
        });
    }

    private _checkInputs(schema: Schema, data: SchematizedData): void {
        schemaUtil.checkSchema(schema);
        utility.requireArgument(data, 'data', 'object');

        let vEntries: ValidationEntry[];
        let savedUserPayload: SchematizedPayload;
        if (data.userPayload !== undefined && schemaUtil.isInternal(schema)) {
            // For internal schemas where we have nested messages, we need to temporarily
            // remove userPayload, since it's not yet encoded. (If left in place, it would fail
            // validation since it's not UInt8Array). We validate it separately here.
            savedUserPayload = data.userPayload;
            vEntries = logValidator.validate(
                savedUserPayload.schema,
                savedUserPayload.payload,
                true
            );
            this._processValidationResults(
                schemaUtil.getSchemaId(savedUserPayload.schema),
                savedUserPayload.payload,
                vEntries
            );
            data.userPayload = undefined;
        }

        vEntries = logValidator.validate(schema, data, true);
        this._processValidationResults(schemaUtil.getSchemaId(schema), data, vEntries);

        if (savedUserPayload !== undefined) {
            // Restore userPayload
            data.userPayload = savedUserPayload;
        }
    }

    private _processValidationResults(
        schemaId: string,
        data: SchematizedData,
        ventries: ValidationEntry[]
    ): void {
        for (let i = 0; i < ventries.length; i += 1) {
            const ei = ventries[i];

            if (
                ei.errorCode == ErrorCode.stringLengthOverflowAppLimit ||
                ei.errorCode == ErrorCode.itemCountOverflowAppLimit
            ) {
                const lfi = ei.fields.length - 1;
                const obj = this._traverseFields(data, ei.fields.slice(0, lfi));
                const field = ei.fields[lfi];
                if (ei.errorCode == ErrorCode.stringLengthOverflowAppLimit) {
                    obj[field] = obj[field].substring(0, maxStringLengthAppLimit);
                } else {
                    obj[field].splice(maxItemCountAppLimit);
                }
            } else {
                throw new Error(ei.asMessage(schemaId));
            }
        }
    }

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    private _traverseFields(data: SchematizedData, fields: string[]): any {
        if (!fields.length) {
            return data;
        }
        return this._traverseFields(data[fields[0]], fields.slice(1));
    }

    getUpCounters(): Metric<number>[] {
        return this._metrics.getUpCounters().filter((m) => m.getLastUpdatedOn());
    }

    getValueRecorders(): Metric<number[]>[] {
        return this._metrics.getValueRecorders().filter((m) => m.getLastUpdatedOn());
    }

    getBucketHistograms(): BucketMetric<number[]>[] {
        return this._metrics.getBucketHistograms().filter((m) => m.getLastUpdatedOn());
    }

    registerForLogPrompt(listener: LogPromptListener): void {
        utility.requireArgument(listener, 'listener', 'function');
        this._nextGen.registerForLogPrompt(listener);
    }

    activity<T>(name: string, execute: (act: Activity) => T, options?: ActivityApiOptions): T {
        const act = this.startActivity(name, options);
        try {
            return execute(act);
        } catch (err) {
            act.error(err, options?.errorPayload?.schema, options?.errorPayload?.payload);
            throw err;
        } finally {
            act.stop(options?.stopPayload?.schema, options?.stopPayload?.payload);
        }
    }

    async activityAsync<T>(
        name: string,
        execute: (act: Activity) => Promise<T>,
        options?: ActivityApiOptions
    ): Promise<T> {
        const act = this.startActivity(name, options);
        try {
            return await execute(act);
        } catch (err) {
            act.error(err, options?.errorPayload?.schema, options?.errorPayload?.payload);
            throw err;
        } finally {
            act.stop(options?.stopPayload?.schema, options?.stopPayload?.payload);
        }
    }
}
