import { TaskerMulti, TaskerSingle } from '../interfaces/IdleDetector';

export type TaskDoneNotifier = () => void;

export class TaskerImpl implements TaskerSingle, TaskerMulti {
    private _busyCount = 0;
    private _isOk = true; // Once an error occurs, make operations no-op

    get isBusy(): boolean {
        return this._isOk ? this._busyCount > 0 : undefined;
    }

    constructor(
        public readonly name: string,
        initialCount: number,
        private readonly _doneCallback: TaskDoneNotifier,
        private readonly _overDoneCallback: TaskDoneNotifier
    ) {
        this._busyCount = initialCount;
    }

    add(): void {
        if (this._isOk) {
            this._busyCount += 1;
        }
    }
    done(): void {
        if (this._isOk) {
            if (this._busyCount > 0) {
                this._busyCount -= 1;
                if (!this._busyCount) {
                    this._doneCallback();
                }
            } else {
                this._isOk = false;
                this._overDoneCallback();
            }
        }
    }
}
