import { Activity, TracingHeadersOptions } from '../interfaces';

const id: string = new Array(16).fill(0).join('');
class ActivityNoOp implements Activity {
    getId(): string {
        return id;
    }
    getRootId(): string {
        return undefined;
    }
    /* eslint-disable-next-line @typescript-eslint/no-empty-function */
    error(): void {}
    /* eslint-disable-next-line @typescript-eslint/no-empty-function */
    stop(): void {}
    /* eslint-disable-next-line @typescript-eslint/no-empty-function */
    discard(): void {}
    /* eslint-disable-next-line @typescript-eslint/no-empty-function */
    terminate(): void {}

    getIsSampled(): boolean {
        return false;
    }
    getTraceHeaders(__options: TracingHeadersOptions): Record<string, string> {
        return {};
    }
    getStartTimestamp(): number {
        return undefined;
    }
    getStartPerfTime(): number {
        return undefined;
    }
    getStopPerfTime(): number {
        return undefined;
    }
}

export const activityNoOp = Object.freeze(new ActivityNoOp());
