import { Metric } from '../interfaces/Metric';
import { UpCounter } from '../interfaces/UpCounter';
import { MetricsTags, utility } from 'o11y/shared';

export class UpCounterImpl implements Metric<number>, UpCounter {
    private readonly _createdOn: number;
    private _lastUpdatedOn: number;
    private _value = 0;

    constructor(
        private readonly _name: string,
        private readonly _ownerName: string,
        private readonly _ownerAppName: string,
        private readonly _tags: MetricsTags,
        createdOn?: number,
        lastUpdatedOn?: number,
        value?: number
    ) {
        this._createdOn = createdOn === undefined ? utility.time().tsNow : createdOn;
        if (lastUpdatedOn !== undefined) {
            this._lastUpdatedOn = lastUpdatedOn;
        }
        if (value !== undefined) {
            this._value = value;
        }
    }

    getName(): string {
        return this._name;
    }

    getCreatedOn(): number {
        return this._createdOn;
    }

    getLastUpdatedOn(): number {
        return this._lastUpdatedOn;
    }

    getData(): number {
        return this._value;
    }

    increment(value = 1): void {
        if (typeof value === 'number' && value > 0) {
            this._value += Math.round(value);
            this._lastUpdatedOn = utility.time().tsNow;
            return;
        }

        throw new Error('UpCounter can only increment positive numbers.');
    }

    reset(): void {
        this._lastUpdatedOn = undefined;
        this._value = 0;
    }

    getOwnerName(): string {
        return this._ownerName;
    }

    getOwnerAppName(): string {
        return this._ownerAppName;
    }

    getTags(): Readonly<MetricsTags> {
        return this._tags;
    }
}
